import clsx from 'clsx';
import React, { HTMLAttributes } from 'react';

export const typographyVariants = [
  'h1',
  'h2',
  'h3',
  'h4',
  'paragraph',
  'meta',
  'navigation',
] as const;

interface TypographyProps
  extends HTMLAttributes<
    HTMLParagraphElement & HTMLHeadingElement & HTMLSpanElement
  > {
  element?: 'span' | 'div' | 'p' | 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6';
  variant?: typeof typographyVariants[number];
  autoMargin?: boolean;
  startAdornment?: React.ReactElement;
}

export default function Typography({
  element = 'p',
  variant = 'paragraph',
  autoMargin,
  className,
  children,
  startAdornment,
  ...elementProps
}: TypographyProps) {
  const renderChildren = (
    <>
      {startAdornment ? (
        <span className="nc-typography__startAdornment">
          {React.cloneElement(startAdornment, {
            size: 'none',
          })}
        </span>
      ) : null}
      {children}
    </>
  );

  return React.createElement(
    element,
    {
      className: clsx(className, 'nc-typography', `nc-typography--${variant}`, {
        'nc-typography--autoMargin': autoMargin,
      }),
      ...elementProps,
    },
    renderChildren
  );
}
