import React, { useEffect, useMemo, useState } from 'react';
import { Link, Outlet } from 'react-router-dom';
import { useCartContext } from '../contexts/CartContextProvider';
import { useConfigurationContext } from '../contexts/ConfigurationProvider';
import { useDispatch, useSelector } from '../hooks';
import { checkoutPaths } from '../routes';
import { setCustomerCartId } from '../store/user';
import {
  CheckoutContext,
  CheckoutContextObjectStore,
  CheckoutRoute,
  CheckoutStep,
  CustomerAddressData,
  MainRoute,
} from '../types';
import CheckoutStepper from './CheckoutStepper';
import Skeleton from './Skeleton';
import Typography from './Typography';

export const checkoutContext = React.createContext<CheckoutContext>({
  customerCart: null,
  confirmStep: () => null,
  contextData: {
    shippingAddressId: null,
    billingAddressId: null,
    billingIsShipping: null,
  },
  setShippingAddressId: () => null,
  setBillingAddressId: () => null,
  setBillingIsShipping: () => null,
  loading: false,
});

export default function Checkout() {
  const dispatch = useDispatch();
  const config = useConfigurationContext();

  const { customer, cartId } = useSelector(({ user }) => user);

  const {
    confirmStep,
    confirmed,
    current,
    completed,
    cart: customerCart,
    loading,
  } = useCartContext();

  // update customer store on cart id change (after place mutation)
  useEffect(() => {
    if (customerCart.id && cartId !== customerCart.id) {
      dispatch(setCustomerCartId(customerCart.id));
    }
  }, [cartId, customerCart.id, customerCart.total_quantity, dispatch]);

  // object store for checkout
  const defaultShipping = Number(customer?.default_shipping);
  const defaultBilling = Number(customer?.default_billing);

  const [contextData, setContextData] = useState<CheckoutContextObjectStore>({
    shippingAddressId: defaultShipping || customer?.addresses?.[0]?.id || 0,
    billingAddressId: defaultBilling || customer?.addresses?.[0]?.id || 0,
    billingIsShipping: defaultShipping === defaultBilling,
  });

  const setShippingAddressId = (id: CustomerAddressData['id']) =>
    setContextData((state) => ({
      ...state,
      shippingAddressId: id,
      billingAddressId: state.billingAddressId || id,
      billingIsShipping:
        id === state.billingAddressId || !state.billingAddressId,
    }));

  const setBillingAddressId = (id: number) =>
    setContextData((state) => ({ ...state, billingAddressId: id }));

  const setBillingIsShipping = () =>
    setContextData((state) => ({
      ...state,
      billingIsShipping: !state.billingIsShipping,
      billingAddressId: !state.billingIsShipping ? state.shippingAddressId : 0,
    }));

  const contextValue = useMemo(
    () => ({
      customerCart: customerCart || null,
      confirmStep,
      contextData,
      setShippingAddressId,
      setBillingAddressId,
      setBillingIsShipping,
      loading,
    }),
    [confirmStep, contextData, customerCart, loading]
  );

  // create stepper steps
  const steps: CheckoutStep[] = useMemo(
    () =>
      checkoutPaths
        .filter((route) => route !== CheckoutRoute.Finale)
        .map((path, index) => ({
          index: index + 1,
          label: config.checkoutStepLabelMap[path],
          current: current === path,
          confirmed: confirmed.includes(path),
          path,
        })),
    [config.checkoutStepLabelMap, confirmed, current]
  );

  const renderCheckout = () =>
    customerCart.total_quantity || completed ? (
      <>
        <CheckoutStepper steps={steps} allComplete={completed} />
        <checkoutContext.Provider value={contextValue}>
          <Outlet />
        </checkoutContext.Provider>
      </>
    ) : (
      <div
        data-cy="nccom-checkout-standalone"
        className="nc-checkout nc-checkout--standalone"
      >
        <div
          data-cy="nccom-checkout-main-summary-empty"
          className="nc-checkout__main nc-checkout__main--summary"
        >
          <Typography variant="h4">There are no items in your cart</Typography>
          <Typography autoMargin>
            <Link
              data-cy="nccom-checkout-product-page-link"
              to={MainRoute.Product}
            >
              Go to products page.
            </Link>
          </Typography>
        </div>
      </div>
    );

  return loading && !customerCart?.total_quantity ? (
    <>
      <div className="nc-checkoutStepper nc-checkoutStepper--skeleton">
        <Skeleton />
      </div>
      <div className="nc-checkout">
        <Skeleton variant="checkout" />
      </div>
    </>
  ) : (
    renderCheckout()
  );
}
