import clsx from 'clsx';
import React from 'react';

interface ListProps {
  items: (React.ReactText | React.ReactNode)[];
  className?: string;
  variant?: 'default' | 'strong';
}

export default function List({ items, className, variant }: ListProps) {
  return (
    <ul className={clsx(className, 'nc-list', `nc-list--${variant}`)}>
      {items.map((item, index) => (
        // eslint-disable-next-line react/no-array-index-key
        <li key={index} className="nc-list__item">
          {item}
        </li>
      ))}
    </ul>
  );
}
