import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useCartContext } from '../contexts/CartContextProvider';
import { useConfigurationContext } from '../contexts/ConfigurationProvider';
import { useNotificationContext } from '../contexts/NotificationProvider';
import { useAddProductToCart } from '../graphql/queries';
import { Voucher } from '../graphql/schema';
import { useSelector } from '../hooks';
import useFetchOptionsOverrides from '../hooks/useFetchOptionsOverrides';
import { CheckoutRoute, MainRoute, ProductSelectedOptions } from '../types';
import AddToCartConfirmationDialog from './AddToCartConfirmationDialog';
import Button, { ButtonProps } from './Button';
import WalletIcon from './icons/WalletIcon';

export interface ProductAddToCartButtonProps
  extends Omit<ButtonProps, 'children'> {
  sku: string;
  quantity?: number;
  options?: ProductSelectedOptions | null;
  voucherCode?: Voucher['code'] | null;
  label?: string;
  visible?: boolean;
}

export default function ProductAddToCartButton({
  sku,
  quantity = 1,
  voucherCode = null,
  options,
  label,
  disabled,
  visible = true,
  ...buttonProps
}: ProductAddToCartButtonProps) {
  const { pushNotification } = useNotificationContext();
  const navigate = useNavigate();

  const config = useConfigurationContext();
  const fetchOptionsOverrides = useFetchOptionsOverrides();
  const { token, cartId } = useSelector((state) => state.user);

  const { getCartItemByProductSku } = useCartContext();

  const [addToCart, { loading }] = useAddProductToCart();

  const [showConfirmationDialog, setShowConfirmationDialog] = useState(false);

  const handleAddToCart = async () => {
    if (!token || !cartId || !voucherCode) return;

    let addToCartQuantity = quantity;

    const cartItemQuantity = getCartItemByProductSku(sku)?.quantity;

    // maximum quantity exceeded
    if (cartItemQuantity && cartItemQuantity >= config.app.maximumQuantity) {
      pushNotification({
        type: 'warning',
        message: `You already have the maximum of ${config.app.maximumQuantity} items of this product in the cart.`,
      });
      navigate(`${MainRoute.Checkout}/${CheckoutRoute.Summary}`);
      return;
    }

    // sum quantity exceeds  maximum quantity
    if (
      cartItemQuantity &&
      cartItemQuantity + quantity > config.app.maximumQuantity
    ) {
      addToCartQuantity = config.app.maximumQuantity - cartItemQuantity;
      pushNotification({
        type: 'warning',
        message: `You can only put ${addToCartQuantity} more items in the cart. The quantity was adapted to ${addToCartQuantity} items.`,
      });
    }

    const { error } = await addToCart({
      fetchOptionsOverrides,
      variables: {
        sku,
        quantity: addToCartQuantity,
        cartId,
        selectedOptions: options ? Object.values(options) : undefined,
      },
    });

    if (error) {
      pushNotification({ apiError: error });
    } else {
      setShowConfirmationDialog(true);
    }
  };

  return (
    <>
      {visible && (
        <Button
          data-cy="nccom-add-to-cart-button"
          icon={voucherCode ? <WalletIcon /> : undefined}
          onClick={handleAddToCart}
          disabled={disabled || loading || !voucherCode}
          {...buttonProps}
        >
          {label || config.productListItem.orderButtonLabel}
        </Button>
      )}
      {!loading && (
        <AddToCartConfirmationDialog
          sku={sku}
          isOpen={showConfirmationDialog}
          quantity={quantity}
        />
      )}
    </>
  );
}
