import clsx from 'clsx';
import { useNotificationContext } from '../contexts/NotificationProvider';
import { useRemoveItemFromCart, useUpdateCartItems } from '../graphql/queries';
import { CartItemUpdateInput } from '../graphql/schema';
import { useSelector } from '../hooks';
import useFetchOptionsOverrides from '../hooks/useFetchOptionsOverrides';
import useVoucherSelection from '../hooks/useVoucherSelection';
import { CustomerCartItem } from '../types';
import Button from './Button';
import TrashcanIcon from './icons/TrashcanIcon';
import Image from './Image';
import ProductPrice from './ProductPrice';
import ProductQuantitySelector from './ProductQuantitySelector';
import Typography from './Typography';

interface CheckoutProductItemProps {
  cartItem: CustomerCartItem;
  hideActions?: boolean;
  loading?: boolean;
}

export default function CartItem({
  cartItem,
  hideActions = false,
  loading,
}: CheckoutProductItemProps) {
  const { pushNotification } = useNotificationContext();
  const fetchOptionsOverrides = useFetchOptionsOverrides();

  const { cartId } = useSelector(({ user }) => user);

  const {
    uid: cartItemUid,
    quantity,
    product,
    configurable_options,
  } = cartItem;

  const { productVouchers } = useVoucherSelection();

  // TODO reconsider making updates of configured options in the order summary
  const [updateCartItems, { loading: loadingUpdateCartItem }] =
    useUpdateCartItems();
  const handleUpdateCartItems = async (
    update: CartItemUpdateInput['quantity']
  ) => {
    const { error } = await updateCartItems({
      fetchOptionsOverrides,
      variables: {
        cartId: cartId as string,
        cartItemUid,
        quantity: update,
      },
    });

    if (error) {
      pushNotification({ apiError: error });
    }
  };

  const [removeCartItem, { loading: loadingRemoveCartItem }] =
    useRemoveItemFromCart();

  const handleRemoveCartItem = async () => {
    const { error } = await removeCartItem({
      fetchOptionsOverrides,
      variables: {
        cartId: cartId as string,
        cartItemUid,
      },
    });

    if (error) {
      pushNotification({ apiError: error });
    }
  };

  return (
    <div
      data-cy="nccom-cart-item"
      className={clsx('nc-cartItem', {
        'nc-cartItem--hiddenActions': hideActions,
      })}
    >
      <div className="nc-cartItem__aside">
        <Image src={product.thumbnail?.url || 'fallback?'} />
        {!hideActions && (
          <Button
            data-cy="nccom-remove-cart-item-button"
            className="nc-cartItem__remove"
            variant="text"
            onClick={handleRemoveCartItem}
            disabled={loadingRemoveCartItem || loadingUpdateCartItem || loading}
            icon={<TrashcanIcon />}
          >
            Remove
          </Button>
        )}
      </div>
      <div data-cy="nc-cart-item-main" className="nc-cartItem__main">
        <div
          data-cy="nccom-cart-item-description"
          className="nc-cartItem__description"
        >
          <Typography data-cy="nccom-cart-item-description-name" variant="h3">
            {product.name}
          </Typography>
          <Typography
            data-cy="nccom-cart-item-description-price"
            className="nc-cartItem__description__price"
            variant="h3"
          >
            <ProductPrice priceRange={product.price_range} />
          </Typography>
          {configurable_options && (
            <Typography
              data-cy="nccom-cart-item-description-config-product-options"
              className="nc-cartItem__description__options"
            >
              {configurable_options.map((item) => item?.value_label).join(', ')}
            </Typography>
          )}
          {hideActions && (
            <Typography
              data-cy="nccom-cart-item-description-quantity"
              className="nc-cartItem__description__quantity"
            >
              Quantity: {quantity}
            </Typography>
          )}
        </div>
        {!hideActions && (
          <ProductQuantitySelector
            className="nc-cartItem__quantity"
            value={quantity}
            onChange={handleUpdateCartItems}
            loading={loadingRemoveCartItem || loadingUpdateCartItem || loading}
            maximumQuantity={
              productVouchers?.[product.sku as string]?.all?.length
            }
          />
        )}
      </div>
    </div>
  );
}
