import clsx from 'clsx';
import React, { ReactNode } from 'react';
import Typography from './Typography';

interface CheckoutSectionProps {
  header?: string;
  action?: ReactNode;
  children: React.ReactNode;
  dense?: boolean;
}

export default function CheckoutSection({
  header,
  action,
  children,
  dense,
}: CheckoutSectionProps) {
  return (
    <section data-cy="nccom-checkout-section" className="nc-checkout__section">
      {(header || action) && (
        <header
          data-cy={`nccom-checkout-${header
            ?.toLowerCase()
            .replaceAll(' ', '-')}-header`}
          className="nc-checkout__section__header"
        >
          <Typography variant="h4" className="">
            {header}
          </Typography>
          <span
            data-cy="nc-checkout-section-action"
            className="nc-checkout__section__action"
          >
            {action}
          </span>
        </header>
      )}
      <div
        data-cy="nccom-checkout-section-content"
        className={clsx('nc-checkout__section__content', {
          'nc-checkout__section__content--dense': dense,
        })}
      >
        {children}
      </div>
    </section>
  );
}
