import clsx from 'clsx';
import React from 'react';
import useSanitizedAddress from '../hooks/useSanitizedAddress';
import { AddressDisplayInput } from '../types';
import List from './List';
import Typography from './Typography';

export interface CustomerAddressDisplayProps {
  address: AddressDisplayInput;
  className?: string;
  displayDefaults?: 'none' | 'all' | 'shipping' | 'billing';
}

export default function CustomerAddressDisplay({
  address: rawAddress,
  className,
  displayDefaults = 'all',
}: CustomerAddressDisplayProps) {
  const address = useSanitizedAddress(rawAddress);

  return (
    <div
      data-cy="nccom-customer-address-display"
      className={clsx(className, 'nc-customer__addressDisplay')}
    >
      <List
        items={[
          `${address.firstname} ${address.lastname}`,
          address.street?.join(', '),
          `${address.postcode} ${address.city}`,
          `${address.countryCode} ${address.countryName}${
            address.region ? `, ${address.region}` : ''
          }`,
        ]}
      />
      {displayDefaults !== 'none' &&
        (address.default_billing || address.default_shipping) && (
          <Typography
            variant="meta"
            className="nc-customer__addressDisplay__defaults"
          >
            {['all', 'shipping'].includes(displayDefaults) &&
              address.default_shipping && <span>Default shipping</span>}
            {['all', 'billing'].includes(displayDefaults) &&
              address.default_billing && <span>Default billing</span>}
          </Typography>
        )}
    </div>
  );
}
