import clsx from 'clsx';
import { PaginationMeta } from '../types';
import Button from './Button';
import ReturnIcon from './icons/ReturnIcon';
import Select from './Select';
import Typography from './Typography';

interface PaginationProps {
  count?: number;
  currentPage?: number;
  onPageChange?: (page: number) => void;
  onPreviousPage?: () => void;
  onNextPage?: () => void;
  pageSize?: number;
  pageSizes?: number[];
  onPageSizeChange?: (size: number) => void;
  meta?: PaginationMeta;
  className?: string;
}

export default function Pagination({
  count = 1,
  currentPage = 0,
  onPageChange,
  onPreviousPage,
  onNextPage,
  pageSize = 5,
  pageSizes = [5, 10, 50],
  onPageSizeChange,
  meta,
  className,
}: PaginationProps) {
  return (
    <div
      data-cy="nccom-pagination"
      className={clsx(className, 'nc-pagination')}
    >
      <div data-cy="nccom-pagination-pages" className="nc-pagination__pages">
        <Button
          data-cy="nccom-pagination-previous-button"
          onClick={onPreviousPage}
          disabled={currentPage === 0}
        >
          <ReturnIcon />
        </Button>

        {Array(count)
          .fill(null)
          .map((_, page) => (
            <Button
              // eslint-disable-next-line react/no-array-index-key
              key={page}
              onClick={onPageChange ? () => onPageChange(page) : undefined}
              variant={page === currentPage ? 'primary' : 'secondary'}
            >
              {page + 1}
            </Button>
          ))}
        <Button
          data-cy="nccom-pagination-next-button"
          onClick={onNextPage}
          disabled={currentPage === count - 1}
        >
          <ReturnIcon className="nc-pagination__next" />
        </Button>
      </div>

      <div
        data-cy="nccom-pagination-control"
        className="nc-pagination__control"
      >
        {meta && (
          <Typography
            data-cy="nccom-pagination-control-meta"
            className="nc-pagination__control__meta"
          >
            Showing
            <strong>{meta.from}</strong> to
            <strong>{meta.to}</strong> of
            <strong>{meta.total}</strong>
          </Typography>
        )}
        <div className="nc-pagination__control__pageSize">
          <Select
            options={pageSizes?.map((value) => ({ value }))}
            value={pageSize}
            onChange={
              onPageSizeChange
                ? ({ value }) => onPageSizeChange(Number(value))
                : undefined
            }
          />
        </div>
      </div>
    </div>
  );
}
