import clsx from 'clsx';
import { Link } from 'react-router-dom';
import { useConfigurationContext } from '../contexts/ConfigurationProvider';
import { ConfigurableProductOptions } from '../graphql/schema';
import useVoucherSelection from '../hooks/useVoucherSelection';
import { ProductItem } from '../types';
import Card from './Card';
import ProductAddToCartButton from './ProductAddToCartButton';
import ProductAddToCartDialog from './ProductAddToCartDialog';
import ProductPrice from './ProductPrice';
import Typography from './Typography';
import Image from './Image';

interface ProductListItemProps {
  product: ProductItem;
}

export default function ProductListItem({ product }: ProductListItemProps) {
  const config = useConfigurationContext();
  const { sku, name, thumbnail, price_range, configurable_options } = product;

  const { productVouchers } = useVoucherSelection();

  const addToCartProps = {
    sku: sku as string,
    className: 'nc-productListItem__order',
    voucherCode:
      productVouchers?.[product.sku as string]?.available?.[0] || null,
  };

  return (
    <Card
      className={clsx('nc-productListItem', {
        'nc-productListItem--isSimple': !configurable_options,
        'nc-productListItem--isConfigurable': configurable_options,
      })}
    >
      {/* TODO replace with image component once available */}
      <Image
        data-cy="nccom-product-image"
        className="nc-productListItem__image"
        src={thumbnail?.url || 'fallback?'}
        alt="TODO"
      />
      <div data-cy="nccom-product-info" className="nc-productListItem__info">
        <Typography
          data-cy="nccom-product-name"
          className="nc-productListItem__name"
          variant="h3"
          element="h3"
        >
          {name}
        </Typography>
        <Typography
          data-cy="nccom-product-price"
          className="nc-productListItem__price"
        >
          <ProductPrice priceRange={price_range} />
        </Typography>
      </div>
      <div className="nc-productListItem__action">
        <Link
          data-cy="nccom-product-info-link"
          className="nc-productListItem__link nc-productListItem__action__link"
          to={sku as string}
        >
          {config.productListItem.detailLink}
        </Link>
        {configurable_options ? (
          <ProductAddToCartDialog
            {...addToCartProps}
            options={configurable_options as ConfigurableProductOptions[]}
          />
        ) : (
          <ProductAddToCartButton {...addToCartProps} />
        )}
      </div>
    </Card>
  );
}
