import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useCartContext } from '../contexts/CartContextProvider';
import { useConfigurationContext } from '../contexts/ConfigurationProvider';
import { MainRoute } from '../types';
import Button from './Button';
import NotificationSuccessIcon from './icons/NotificationSuccessIcon';
import Modal from './Modal';
import ProductPrice from './ProductPrice';
import Typography from './Typography';
import Image from './Image';
import Divider from './Divider';

interface AddToCartConfirmationDialogProps {
  sku: string;
  quantity: number;
  isOpen: boolean;
}

export default function AddToCartConfirmationDialog({
  sku,
  quantity,
  isOpen,
}: AddToCartConfirmationDialogProps) {
  const { addToCartSuccessMessage } = useConfigurationContext();
  const [modalOpen, setModalOpen] = useState(false);
  const { getCartItemByProductSku, loading } = useCartContext();
  const product = getCartItemByProductSku(sku)?.product;

  useEffect(() => {
    if (isOpen && !loading) {
      setModalOpen(true);
    }
  }, [isOpen, loading]);

  const navigate = useNavigate();

  const handleContinueShopping = () => {
    setModalOpen(false);
    navigate(MainRoute.Product);
  };

  const handleGoToCart = () => {
    setModalOpen(false);
    navigate(MainRoute.Cart);
  };

  return (
    <Modal
      className="nc-addToCartConfirmationDialog__modal"
      isOpen={modalOpen}
      onClose={() => setModalOpen(false)}
    >
      <div className="nc-addToCartConfirmationDialog__successText">
        <NotificationSuccessIcon />
        <Typography>{addToCartSuccessMessage}</Typography>
      </div>
      <div className="nc-addToCartConfirmationDialog__product">
        <Image
          className="nc-addToCartConfirmationDialog__productThumbnail"
          src={product?.thumbnail?.url || 'fallback?'}
          alt="product thumbnail"
        />
        <div className="nc-addToCartConfirmationDialog__productInfo">
          <Typography variant="h4" element="h3">
            {product?.name}
          </Typography>
          <div className="nc-addToCartConfirmationDialog__productDetails">
            <Typography>Quantity: {quantity}</Typography>
            <Typography>
              Price:{' '}
              {product?.price_range ? (
                <ProductPrice priceRange={product?.price_range} />
              ) : (
                0
              )}
            </Typography>
          </div>
        </div>
      </div>
      <Divider color="primary" />
      <div className="nc-addToCartConfirmationDialog__actions">
        <Button
          data-cy="nccom-confirmation-continue-button"
          onClick={handleContinueShopping}
          variant="secondary"
        >
          Continue shopping
        </Button>
        <Button
          data-cy="nccom-confirmation-go-to-cart-button"
          onClick={handleGoToCart}
          variant="primary"
        >
          Go to cart
        </Button>
      </div>
    </Modal>
  );
}
