import clsx from 'clsx';
import React, { InputHTMLAttributes } from 'react';

export interface RadioButtonProps
  extends InputHTMLAttributes<HTMLInputElement> {
  label?: string | React.ReactNode;
}

export default function RadioButton({
  label,
  id,
  className,
  ...radioButtonProps
}: RadioButtonProps) {
  return (
    <label
      className={clsx(className, 'nc-formControlLabel', {
        'nc-formControlLabel--isDisabled': radioButtonProps.disabled,
      })}
      htmlFor={id}
    >
      <input
        data-cy="nccom-radio-button"
        className="nc-formControlLabel__input"
        type="radio"
        id={id}
        {...radioButtonProps}
      />
      <span className="nc-formControlLabel__control nc-formControlLabel__control--radio" />
      {label && <span className="nc-formControlLabel__label">{label}</span>}
    </label>
  );
}
