import clsx from 'clsx';
import { ImgHTMLAttributes } from 'react';

interface ImageProps extends ImgHTMLAttributes<HTMLImageElement> {
  src: string; // enforcing src as required
  withOverlay?: boolean;
}

export default function Image({
  className,
  alt = '',
  withOverlay,
  loading = 'lazy',
  ...imgProps
}: ImageProps) {
  return (
    <div
      data-cy="nccom-image"
      className={clsx(className, 'nc-image', {
        'nc-image--withOverlay': withOverlay,
      })}
    >
      <img
        className={clsx('nc-image__img')}
        loading={loading}
        alt={alt}
        {...imgProps}
      />
    </div>
  );
}
