import { useCallback, useEffect, useMemo, useState } from 'react';

type UsePaginationInitials = {
  initialTotalCount: number;
  initialPage: number;
  initialPageSize: number;
};

export default function usePagination(initials?: UsePaginationInitials) {
  const { initialTotalCount, initialPage, initialPageSize } = initials || {
    initialTotalCount: 0,
    initialPage: 0,
    initialPageSize: 10,
  };
  const [totalCount, setTotalCount] = useState(initialTotalCount);
  const [currentPage, setCurrentPage] = useState(initialPage);
  const [pageSize, setPageSize] = useState(initialPageSize);

  useEffect(() => {
    setCurrentPage(0);
  }, [totalCount]);

  return {
    currentPage,
    setCurrentPage,
    pageSize,
    setPageSize,
    totalCount,
    setTotalCount,
    pageCount: useMemo(
      () => Math.floor(totalCount / pageSize) + (totalCount % pageSize ? 1 : 0),
      [totalCount, pageSize]
    ),
    setPreviousPage: useCallback(() => {
      if (currentPage > 0) {
        setCurrentPage((state) => state - 1);
      }
    }, [currentPage]),
    setNextPage: useCallback(() => {
      if (currentPage < totalCount / pageSize) {
        setCurrentPage((state) => state + 1);
      }
    }, [currentPage, pageSize, totalCount]),
    meta: useMemo(
      () => ({
        from: totalCount ? currentPage * pageSize + 1 : 0,
        to:
          currentPage * pageSize + pageSize < totalCount
            ? currentPage * pageSize + pageSize
            : totalCount,
        total: totalCount,
      }),
      [currentPage, pageSize, totalCount]
    ),
  };
}
