import { APIError } from 'graphql-hooks';
import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useState,
} from 'react';
import { GraphQlError, Notification } from '../types';
import createUid from '../util/createUid';
import { useConfigurationContext } from './ConfigurationProvider';

export type NotificationInput = Pick<Notification, 'type' | 'autoDismiss'> & {
  apiError?: APIError<GraphQlError>;
  message?: Notification['message'];
};

export interface NotificationContext {
  currentNotification: Notification | null;
  pushNotification: (data: NotificationInput) => void;
  dismissNotification: (uid: Notification['uid']) => void;
  currentNotificationClass: 'isActive' | 'isGoing';
}

const context = React.createContext<NotificationContext>({
  currentNotification: null,
  pushNotification: () => null,
  dismissNotification: () => null,
  currentNotificationClass: 'isActive',
});

export function useNotificationContext() {
  return useContext(context);
}

interface NotificationProviderProps {
  children: React.ReactNode;
}

export default function NotificationProvider({
  children,
}: NotificationProviderProps) {
  const {
    app: { notificationAutoDismissTime, notificationDisappearTime },
  } = useConfigurationContext();
  const [notifications, setNotifications] = useState<Notification[]>([]);

  const [currentNotificationClass, setCurrentNotificationClass] = useState<
    'isActive' | 'isGoing'
  >('isActive');

  const createApiErrorMessage = (apiError: APIError<GraphQlError>): string =>
    Object.entries(apiError)
      .map(([errorType, error]) => {
        switch (errorType as keyof APIError) {
          case 'fetchError':
            return (error as APIError<GraphQlError>['fetchError'])?.message;
          case 'httpError':
            return (error as APIError<GraphQlError>['httpError'])?.body;
          case 'graphQLErrors':
            return (error as APIError<GraphQlError>['graphQLErrors'])
              ?.map((item) => item.message)
              .join('\n');
          default:
            return 'unknown error type';
        }
      })
      .join('\n');

  const pushNotification = useCallback((data: NotificationInput) => {
    const notification: Notification = {
      type: data.apiError ? 'error' : 'default',
      autoDismiss: false,
      ...data,
      message: data.apiError
        ? createApiErrorMessage(data.apiError)
        : data.message || 'Notification',
      date: new Date(),
      uid: createUid(),
    };

    setNotifications((state) => [...state, notification]);
  }, []);

  const removeNotification = useCallback(
    (uid: Notification['uid']) => {
      const index = notifications.findIndex((item) => item.uid === uid);
      if (index == null) return;

      const nextNotifications = [...notifications];
      nextNotifications.splice(index, 1);

      setNotifications(nextNotifications);
    },
    [notifications]
  );

  const dismissNotification = () => {
    setCurrentNotificationClass('isGoing');
  };

  useEffect(() => {
    let timer: number;

    if (currentNotificationClass === 'isGoing') {
      timer = setTimeout(() => {
        removeNotification(notifications[0].uid);
        setCurrentNotificationClass('isActive');
        clearTimeout(timer);
      }, notificationDisappearTime) as unknown as number;
    }

    return () => clearTimeout(timer);
  }, [
    currentNotificationClass,
    notificationDisappearTime,
    notifications,
    removeNotification,
  ]);

  // handle auto dismiss
  useEffect(() => {
    if (!notifications.length) return undefined;

    let timer: number;

    if (notifications[0].autoDismiss) {
      timer = setTimeout(() => {
        dismissNotification();
        clearTimeout(timer);
      }, notificationAutoDismissTime) as unknown as number;
    }

    return () => clearTimeout(timer);
  }, [removeNotification, notificationAutoDismissTime, notifications]);

  const contextValue: NotificationContext = useMemo(
    () => ({
      currentNotification: notifications[0],
      pushNotification,
      dismissNotification,
      currentNotificationClass,
    }),
    [currentNotificationClass, notifications, pushNotification]
  );

  return <context.Provider value={contextValue}>{children}</context.Provider>;
}
