import React from 'react';
import { useAddressManagementContext } from '../contexts/AddressManagementProvider';
import { CustomerAddressData } from '../types';
import Checkbox from './Checkbox';

interface CustomerAddressDefaultsHandlerProps {
  address: Partial<CustomerAddressData>;
}

export default function CustomerAddressDefaultsHandler({
  address,
}: CustomerAddressDefaultsHandlerProps) {
  const { handleUpdate, busy } = useAddressManagementContext();

  return (
    <div
      data-cy="nccom-customer-address-defaults"
      className="nc-customer__addressDefaults"
    >
      <Checkbox
        data-cy="nccom-shipping-address-checkbox"
        label="Default shipping"
        id={`setDefaultShipping_${address.id}`}
        checked={!!address.default_shipping}
        onChange={() =>
          handleUpdate(address.id as number, {
            default_shipping: !address.default_shipping,
          })
        }
        disabled={busy}
      />
      <Checkbox
        data-cy="nccom-billing-address-checkbox"
        label="Default billing"
        id={`setDefaultBilling_${address.id}`}
        checked={!!address.default_billing}
        onChange={() =>
          handleUpdate(address.id as number, {
            default_billing: !address.default_billing,
          })
        }
        disabled={busy}
      />
    </div>
  );
}
