import clsx from 'clsx';
import React, { useContext, useState } from 'react';

const tabContext = React.createContext<string | number>(0);

interface TabsItemProps {
  id: string | number;
  label: string;
  disabled?: boolean;
  children: React.ReactElement;
}

export function TabsItem({ id, children }: TabsItemProps) {
  const active = useContext(tabContext);

  return active === id ? children : null;
}

interface TabsProps {
  children: React.ReactElement<TabsItemProps>[];
  initialItem?: string | number;
}

export default function Tabs({ children, initialItem = 0 }: TabsProps) {
  const [active, setActive] = useState<string | number>(initialItem);

  const tabNavigation = children.map(({ props: { id, disabled, label } }) => ({
    id,
    label,
    disabled,
  }));

  return (
    <div className="nc-tabs">
      <div className="nc-tabs__header">
        {tabNavigation.map((item) => (
          <button
            data-cy={`nccom-${item.id.toString().replaceAll(' ', '-')}-tab`}
            className={clsx('nc-tabs__button', {
              'nc-tabs__button--isActive': item.id === active,
              'nc-tabs__button--isDisabled': item.disabled,
            })}
            key={item.id}
            onClick={item.disabled ? undefined : () => setActive(item.id)}
          >
            {item.label}
          </button>
        ))}
      </div>
      <tabContext.Provider value={active}>
        <div data-cy="nccom-tabs-item" className="nc-tabs__item">
          {children}
        </div>
      </tabContext.Provider>
    </div>
  );
}
