import { useCartContext } from '../contexts/CartContextProvider';
import { ButtonProps } from './Button';
import IconButton from './IconButton';
import ShoppingBagIcon from './icons/ShoppingBag';
import WidgetCounter from './WidgetCounter';

export default function WidgetCart(props: ButtonProps) {
  const { cart } = useCartContext();

  return (
    <div className="nc-widget__item">
      <IconButton
        data-cy="nccom-widget-cart-icon-button"
        className="nc-widget__cart"
        {...props}
      >
        <ShoppingBagIcon size="none" />
        <WidgetCounter
          dataCy="nccom-widget-cart-count"
          count={cart.total_quantity}
        />
      </IconButton>
    </div>
  );
}
