import clsx from 'clsx';
import React, { ButtonHTMLAttributes } from 'react';

export interface ButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  variant?: 'primary' | 'secondary' | 'text';
  icon?: React.ReactElement;
  fullWidth?: boolean;
  // link props (to inherited from actual link type)
  href?: string;
  target?: string;
}

export default function Button({
  variant = 'primary',
  children,
  icon,
  fullWidth,
  className,
  type = 'button',
  href,
  target,
  ...buttonProps
}: ButtonProps) {
  const iconProps = {
    focusable: false,
  };

  const linkProps = {
    href,
    target,
  };

  const renderChildren = (
    <>
      {icon && (
        <span data-cy="nc-button-icon" className="nc-button__icon">
          {React.cloneElement(icon, iconProps)}
        </span>
      )}
      <span className="nc-button__content">{children}</span>
    </>
  );

  return React.createElement(
    href ? 'a' : 'button',
    {
      'data-cy': `nccom-${children
        ?.toString()
        .toLowerCase()
        .replaceAll(' ', '-')}-button`,
      className: clsx(className, 'nc-button', `nc-button--${variant}`, {
        'nc-button--icon': icon,
        'nc-button--isFullWidth': fullWidth,
        'nc-button--isDisabled': buttonProps.disabled,
      }),
      'aria-disabled': buttonProps.disabled,
      type: href ? undefined : type,
      ...buttonProps,
      ...linkProps,
    },
    renderChildren
  );
}
