import { useEffect } from 'react';
import { useParams } from 'react-router-dom';
import AddressManagementProvider from '../contexts/AddressManagementProvider';
import { useDispatch, useSelector } from '../hooks';
import { setStageContent } from '../store/ui';
import { AccountSection } from '../types';
import AccountAddresses from './AccountAddresses';
import AccountOrderHistory from './AccountOrderHistory';
import Tabs, { TabsItem } from './Tabs';

export default function Account() {
  const dispatch = useDispatch();
  const user = useSelector((state) => state.user);

  const { section } = useParams<{ section: AccountSection }>();

  useEffect(() => {
    dispatch(
      setStageContent({
        title: `Hello ${user.customer?.firstname} ${user.customer?.lastname}`,
      })
    );
  }, [dispatch, user]);

  return (
    <Tabs
      initialItem={
        (section || AccountSection.OrderHistory) as unknown as string
      }
      key={section}
    >
      <TabsItem id={AccountSection.OrderHistory} label="Order history">
        <AccountOrderHistory />
      </TabsItem>
      <TabsItem id={AccountSection.AddressBook} label="Address book">
        <AddressManagementProvider>
          <AccountAddresses user={user} />
        </AddressManagementProvider>
      </TabsItem>
    </Tabs>
  );
}
