import {
  BillingCartAddress,
  CountryCodeEnum,
  ShippingCartAddress,
} from '../graphql/schema';
import { getCountryById } from '../store/ui';
import {
  AddressDisplayData,
  AddressDisplayInput,
  CustomerAddressData,
} from '../types';
import { useSelector } from './index';

export default function useSanitizedAddress(
  rawAddress: AddressDisplayInput
): AddressDisplayData {
  const { firstname, lastname, telephone, street, postcode, city } = rawAddress;

  const { default_shipping, default_billing } =
    rawAddress as CustomerAddressData;

  const countryCode =
    (rawAddress as CustomerAddressData).country_code ||
    (rawAddress as ShippingCartAddress | BillingCartAddress).country.code ||
    '';
  const countryName =
    useSelector(getCountryById(countryCode as CountryCodeEnum))
      ?.full_name_english || '';

  const region =
    (rawAddress as CustomerAddressData).region?.region ||
    (rawAddress as ShippingCartAddress | BillingCartAddress).region?.label ||
    undefined;

  return {
    firstname: firstname || '',
    lastname: lastname || '',
    telephone: telephone || '',
    street: street || [],
    postcode: postcode || '',
    city: city || '',
    countryCode,
    countryName,
    region,
    default_shipping,
    default_billing,
  };
}
