import clsx from 'clsx';
import React, { CSSProperties, useLayoutEffect, useRef, useState } from 'react';
import useClickAwayListener from '../hooks/useClickAwayListener';
import useWindowSize from '../hooks/useWindowSize';
import Card from './Card';

interface MenuProps {
  children: React.ReactNode;
  open?: boolean;
  onClose?: () => void;
  className?: string;
}

export default function Menu({
  children,
  className,
  open = false,
  onClose,
}: MenuProps) {
  const { width } = useWindowSize();
  const containerRef = useRef<HTMLDivElement | null>(null);

  const [styles, setStyles] = useState<CSSProperties | null>({});

  useClickAwayListener({
    containerRef,
    onClickAway: () => {
      if (open && onClose) {
        onClose();
      }
    },
    disable: !styles,
  });

  useLayoutEffect(() => {
    setStyles(null);

    if (!containerRef.current || !open) return;

    containerRef.current.style.width = '';

    const menu = containerRef.current;
    const parent = menu.parentElement;

    if (menu && parent) {
      const gutter = 16;

      const nextStyles: CSSProperties = {
        position: 'absolute',
        top: 0,
        right: 0,
      };

      const offset = {
        x: 0,
        y: 0,
      };

      const parentWidth = parent.offsetWidth;
      const compensateMenuWidth = menu.offsetWidth > document.body.offsetWidth;
      const compensateMenuPosition =
        compensateMenuWidth ||
        menu.offsetWidth > parent.offsetLeft + parentWidth + gutter;

      // width
      const menuWidth = compensateMenuWidth
        ? document.body.offsetWidth - gutter * 2
        : menu.offsetWidth;
      nextStyles.width = `${menuWidth}px`;

      // translate
      offset.x = compensateMenuPosition
        ? width - (parent.offsetLeft + parentWidth) - gutter * 2
        : gutter;
      offset.y = parent.offsetHeight;
      nextStyles.transform = `translate3d(${offset.x}px,${offset.y}px,0)`;

      setStyles(nextStyles);
    }
  }, [open, width]);

  return open ? (
    <Card
      className={clsx(className, 'nc-menu', {
        'nc-menu--isOpen': open,
      })}
      ref={containerRef}
      style={styles || undefined}
    >
      {children}
    </Card>
  ) : null;
}
