import clsx from 'clsx';
import React from 'react';
import { LabelListItem } from '../types';

interface LabelListProps {
  items: LabelListItem[];
  className?: string;
  variant?: 'default' | 'dense';
}

export default function LabelList({
  items,
  className,
  variant = 'default',
}: LabelListProps) {
  return (
    <ul className={clsx(className, 'nc-labelList', `nc-labelList--${variant}`)}>
      {items.map((item, index) => (
        <li
          // eslint-disable-next-line react/no-array-index-key
          key={item.label + index}
          className="nc-labelList__item"
        >
          <span
            data-cy={`nccom-${item.label
              .replaceAll(' ', '-')
              .replaceAll(/[*]/g, '')
              .toLowerCase()}-label`}
            className="nc-labelList__label"
          >
            {item.label}
          </span>
          <span
            data-cy="nccom-label-list-content"
            className="nc-labelList__content"
          >
            {item.content}
          </span>
        </li>
      ))}
    </ul>
  );
}
