import { createSelector, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { CountriesQuery, CountryCodeEnum } from '../graphql/schema';
import { RegionMap, RootState, StageContent } from '../types';

// Define a type for the slice state
export interface UiState {
  stage: StageContent;
  countries: CountriesQuery['countries'];
}

// Define the initial state using that type
const initialState: UiState = {
  stage: {
    title: 'Stage Title',
    description: 'Stage description',
    loading: false,
  },
  countries: null,
};

export const { reducer, actions } = createSlice({
  name: 'ui',
  // `createSlice` will infer the state type from the `initialState` argument
  initialState,
  /* eslint-disable no-param-reassign */
  reducers: {
    setStageContent: (state, { payload }: PayloadAction<UiState['stage']>) => {
      state.stage = payload;
    },
    setCountries: (state, { payload }: PayloadAction<UiState['countries']>) => {
      state.countries = payload;
    },
  },
  /* eslint-enable no-param-reassign */
});

export const { setStageContent, setCountries } = actions;

export const getCountries = (state: RootState) => state.ui.countries;
export const getRegionsMap = (state: RootState): RegionMap =>
  state.ui.countries?.reduce(
    (acc, country) =>
      country && country.available_regions?.length
        ? { ...acc, [country.id as string]: country.available_regions }
        : acc,
    {}
  ) || {};
export const getCountryById = (countryId: CountryCodeEnum) =>
  createSelector(
    getCountries,
    (
      state
    ):
      | NonNullable<NonNullable<CountriesQuery['countries']>[number]>
      | undefined
      | null => state?.find((item) => item?.id === countryId)
  );

export default reducer;
