import React, { useState } from 'react';
import { useAddressManagementContext } from '../contexts/AddressManagementProvider';
import { CustomerAddressInput } from '../graphql/schema';
import { CustomerAddressData, RootState } from '../types';
import Button from './Button';
import CheckoutSection from './CheckoutSection';
import CustomerAddressDefaultsHandler from './CustomerAddressDefaultsHandler';
import CustomerAddressDisplay from './CustomerAddressDisplay';
import CustomerAddressForm from './CustomerAddressForm';
import Divider from './Divider';
import IconButton from './IconButton';
import CloseIcon from './icons/CloseIcon';
import EditIcon from './icons/EditIcon';
import PlusIcon from './icons/PlusIcon';
import TrashcanIcon from './icons/TrashcanIcon';
import Typography from './Typography';

interface AccountAddressesProps {
  user: RootState['user'];
}

export default function AccountAddresses({ user }: AccountAddressesProps) {
  const { customer } = user;

  const { handleCreate, handleUpdate, handleDelete, busy } =
    useAddressManagementContext();

  const [updateAddress, setUpdateAddress] =
    useState<Partial<CustomerAddressData> | null>(null);
  const [createAddress, setCreateAddress] =
    useState<Partial<CustomerAddressData> | null>(null);

  const managementActions = (item: CustomerAddressData) => (
    <div className="nc-checkout__section__actionGroup">
      <IconButton
        data-cy="nccom-account-addresses-edit-button"
        onClick={() =>
          updateAddress ? setUpdateAddress(null) : setUpdateAddress(item)
        }
      >
        <EditIcon />
      </IconButton>
      <IconButton
        data-cy="nccom-account-addresses-delete-button"
        title={item.id.toString()}
        onClick={() => handleDelete(item.id)}
        disabled={!item || !!item.default_shipping || !!item.default_billing}
      >
        <TrashcanIcon />
      </IconButton>
    </div>
  );

  const handleUpdateAddress = async (input: CustomerAddressInput) => {
    await handleUpdate(updateAddress?.id as number, input);
    setUpdateAddress(null);
  };

  const handleCreateAddress = async (input: CustomerAddressInput) => {
    await handleCreate(input);
    setCreateAddress(null);
  };

  return (
    <>
      <header
        data-cy="nccom-account-addresses-header"
        className="nc-customer__account__header"
      >
        <Typography variant="h3">Your Adresses</Typography>
        <Button
          icon={createAddress ? <CloseIcon /> : <PlusIcon />}
          onClick={() =>
            createAddress
              ? setCreateAddress(null)
              : setCreateAddress({
                  firstname: customer?.firstname as string,
                  lastname: customer?.lastname as string,
                })
          }
          variant={createAddress ? 'secondary' : 'primary'}
          disabled={busy}
        >
          {createAddress ? 'Cancel' : 'Create'}
        </Button>
      </header>
      <Divider />
      {createAddress && (
        <>
          <CustomerAddressForm
            address={createAddress}
            onSubmit={handleCreateAddress}
            onCancel={() => setCreateAddress(null)}
          />
          <Divider />
        </>
      )}
      {customer?.addresses?.length ? (
        customer?.addresses?.map((item) => (
          <CheckoutSection
            key={item?.id}
            action={managementActions(item as CustomerAddressData)}
            header={item?.city || ''}
            dense
          >
            {updateAddress && updateAddress?.id === item?.id ? (
              <CustomerAddressForm
                address={updateAddress}
                onSubmit={handleUpdateAddress}
                onCancel={() => setUpdateAddress(null)}
                loading={busy}
              />
            ) : (
              <>
                <CustomerAddressDisplay address={item as CustomerAddressData} />
                <CustomerAddressDefaultsHandler
                  address={item as CustomerAddressData}
                />
              </>
            )}
          </CheckoutSection>
        ))
      ) : (
        <Typography>No addresses available</Typography>
      )}
    </>
  );
}
