import React, { useCallback, useContext, useMemo } from 'react';
import Skeleton from '../components/Skeleton';
import {
  AddProductToCartMutationString,
  PlaceOrderMutationString,
  RemoveItemFromCartMutationString,
  SetPaymentMethodMutationString,
  SetShippingAddressMutationString,
  UpdateCartItemsMutationString,
  useCustomerCart,
} from '../graphql/queries';
import { CustomerCartQuery } from '../graphql/schema';
import useCheckoutProcess from '../hooks/useCheckoutProcess';
import useCreateCustomerCart from '../hooks/useCreateCustomerCart';
import useFetchOptionsOverrides from '../hooks/useFetchOptionsOverrides';
import { CartContext, CustomerCartItem, ProductItem } from '../types';

const context = React.createContext<CartContext>({} as CartContext);

export function useCartContext() {
  return useContext(context);
}

interface CartContextProviderProps {
  children: React.ReactNode;
}

export default function CartContextProvider({
  children,
}: CartContextProviderProps) {
  const fetchOptionsOverrides = useFetchOptionsOverrides();

  const { loadingCreateCart } = useCreateCustomerCart();

  const { data, loading, error } = useCustomerCart({
    fetchOptionsOverrides,
    refetchAfterMutations: [
      AddProductToCartMutationString,
      RemoveItemFromCartMutationString,
      UpdateCartItemsMutationString,
      SetShippingAddressMutationString,
      SetPaymentMethodMutationString,
      PlaceOrderMutationString,
    ],
  });

  const { confirmStep, confirmed, current, completed } = useCheckoutProcess();

  // util
  const getCartItemByProductSku = useCallback(
    (sku: ProductItem['sku']) =>
      (data?.customerCart?.items?.find(
        (item) => item?.product?.sku === sku
      ) as CustomerCartItem) || null,
    [data?.customerCart?.items]
  );

  const contextValue = useMemo(
    () => ({
      cart: { ...data?.customerCart } as CustomerCartQuery['customerCart'],
      getCartItemByProductSku,
      loading,
      confirmStep,
      confirmed,
      current,
      completed,
      error: error || null,
    }),
    [
      completed,
      confirmStep,
      confirmed,
      current,
      data?.customerCart,
      getCartItemByProductSku,
      loading,
      error,
    ]
  );

  return (
    <context.Provider value={contextValue}>
      {loadingCreateCart ? <Skeleton count={4} /> : children}
    </context.Provider>
  );
}
