import { useState } from 'react';
import { useAddressManagementContext } from '../contexts/AddressManagementProvider';
import { CustomerAddressInput } from '../graphql/schema';
import { CustomerAddressData } from '../types';
import { useSelector } from './index';

export default function useCreateCheckoutAddress(
  creationSuccessCallback: (newAddressId: CustomerAddressData['id']) => void
) {
  const { customer } = useSelector(({ user }) => user);

  const { handleCreate, busy } = useAddressManagementContext();
  const [createAddress, setCreateAddress] = useState(
    !customer?.addresses?.length
  );
  const handleCreateCustomerAddress = async (input: CustomerAddressInput) => {
    const newAddress = await handleCreate(input);

    if (newAddress?.id) {
      setCreateAddress(false);
      creationSuccessCallback(newAddress.id);
    }
  };

  return {
    createAddress,
    setCreateAddress,
    handleCreateCustomerAddress,
    busy,
  };
}
