import clsx from 'clsx';
import React from 'react';

const variantMarkup: {
  [index: string]: React.ReactElement;
} = {
  default: <div className="nc-skeleton__item" />,
  stage: <div className="nc-skeleton__item" />,
  productList: (
    <>
      <div className="nc-skeleton__item image" />
      <div className="nc-skeleton__item head" />
      <div className="nc-skeleton__item copy" />
    </>
  ),
  productDetail: (
    <>
      <div className="nc-skeleton__item image" />
      <div className="nc-skeleton__item thumb" />
      <div className="nc-skeleton__item copy" />
      <div className="nc-skeleton__item copy" />
      <div className="nc-skeleton__item copy" />
    </>
  ),
  checkout: (
    <>
      <div className="nc-skeleton__item image" />
      <div className="nc-skeleton__item head" />
      <div className="nc-skeleton__item copy" />
      <div className="nc-skeleton__item copy" />
      <div className="nc-skeleton__item summary" />
    </>
  ),
};

interface SkeletonProps {
  variant?: keyof typeof variantMarkup;
  count?: number;
}

export default function Skeleton({
  variant = 'default',
  count = 1,
}: SkeletonProps) {
  return (
    <>
      {Array(count)
        .fill(null)
        .map((_, index) => (
          <div
            // eslint-disable-next-line react/no-array-index-key
            key={index}
            className={clsx('nc-skeleton', `nc-skeleton--${variant}`)}
          >
            {variantMarkup[variant]}
          </div>
        ))}
    </>
  );
}
