import { useEffect } from 'react';
import { useNotificationContext } from '../contexts/NotificationProvider';
import { useManualCustomerCartId } from '../graphql/queries';
import { setCustomerCartId } from '../store/user';
import { useDispatch, useSelector } from './index';
import useFetchOptionsOverrides from './useFetchOptionsOverrides';

/**
 * This hook
 * - retrieves the customer cart
 */
export default function useCreateCustomerCart() {
  const { pushNotification } = useNotificationContext();
  const dispatch = useDispatch();
  const fetchOptionsOverrides = useFetchOptionsOverrides();
  const { token, cartId } = useSelector(({ user }) => user);

  // get magento customer cart
  // -- refreshes customer cart id if it changes to null
  const [getCustomerCartId, { loading: loadingCreateCart = true }] =
    useManualCustomerCartId({
      fetchOptionsOverrides,
      skipCache: true,
    });
  useEffect(() => {
    if (!token || cartId) return;

    getCustomerCartId().then(({ data, error }) => {
      if (error) {
        pushNotification({ apiError: error });
      }
      if (!data) return;
      dispatch(setCustomerCartId(data.customerCart.id));
    });
  }, [cartId, dispatch, getCustomerCartId, pushNotification, token]);

  return {
    loadingCreateCart,
    cartId,
  };
}
