import Card from './Card';
import Divider from './Divider';
import { CustomerOrdersQuery } from '../graphql/schema';
import Typography from './Typography';

interface OrderItemProps {
  order: NonNullable<
    NonNullable<
      NonNullable<
        NonNullable<CustomerOrdersQuery['customer']>['orders']
      >['items']
    >[number]
  >;
}

export default function OrderItem({ order }: OrderItemProps) {
  return (
    <Card key={order.id} className="nc-customer__orderItem">
      <div
        data-cy="nccom-customer-order-item-meta"
        className="nc-customer__orderItem__meta"
      >
        <Typography data-cy="nccom-customer-order-number" variant="meta">
          <strong>{order.order_number}</strong>
        </Typography>
        <Typography data-cy="nccom-customer-order-datetime" variant="meta">
          {order.created_at}
        </Typography>
      </div>
      <Typography data-cy="nccom-customer-order-status">
        Status: {order.status}
      </Typography>
      <Divider />
      <div
        data-cy="nccom-customer-order-items"
        className="nc-customer__orderItem__items"
      >
        {order.items?.map((item) => (
          <div
            key={item?.product_sku}
            data-cy="nccom-customer-order-item"
            className="nc-customer__orderItem__item"
          >
            <Typography
              data-cy="nccom-customer-order-item-product-name"
              variant="h4"
            >
              {item?.product_name}
            </Typography>
            <Typography
              data-cy="nccom-customer-order-item-product-sku"
              variant="meta"
              autoMargin
            >
              SKU: {item?.product_sku}
            </Typography>
            <Typography
              data-cy="nccom-customer-order-item-product-quantity"
              variant="meta"
              autoMargin
            >
              Quantity: {item?.quantity_ordered}
            </Typography>
            {item?.selected_options && (
              <div
                data-cy="nccom-customer-order-item-config-product-options"
                className="nc-customer__orderItem__itemOptions"
              >
                {item?.selected_options?.map(
                  (option) =>
                    option && (
                      <Typography
                        key={option.label + option.value}
                        variant="meta"
                      >
                        {option.label}: {option.value}
                      </Typography>
                    )
                )}
              </div>
            )}
          </div>
        ))}
      </div>
    </Card>
  );
}
