import clsx from 'clsx';
import { useConfigurationContext } from '../contexts/ConfigurationProvider';
import { useNotificationContext } from '../contexts/NotificationProvider';
import Card from './Card';
import NotificationErrorIcon from './icons/NotificationErrorIcon';
import NotificationInfoIcon from './icons/NotificationInfoIcon';
import NotificationSuccessIcon from './icons/NotificationSuccessIcon';
import Typography from './Typography';

export default function NotificationHub() {
  const {
    app: { notificationDisappearTime },
  } = useConfigurationContext();
  const { currentNotification, dismissNotification, currentNotificationClass } =
    useNotificationContext();

  const renderIcon = () => {
    if (
      currentNotification?.type === 'error' ||
      currentNotification?.type === 'warning'
    ) {
      return <NotificationErrorIcon />;
    }
    if (currentNotification?.type === 'success') {
      return <NotificationSuccessIcon />;
    }
    return <NotificationInfoIcon />;
  };

  return currentNotification ? (
    <Card
      className={clsx(currentNotificationClass, 'nc-notificationHub__item')}
      style={{
        animationDuration: `${
          currentNotificationClass === 'isActive'
            ? notificationDisappearTime * 0.75
            : notificationDisappearTime
        }ms`,
      }}
      variant={
        currentNotification.type === 'default'
          ? 'panel'
          : currentNotification.type
      }
    >
      <div className="nc-notificationHub__grid">
        <div className="nc-notificationHub__content">
          <Typography element="div" className="nc-notificationHub__message">
            {renderIcon()}
            {currentNotification.message}
          </Typography>
          <button
            className="nc-notificationHub__dismiss"
            onClick={() => dismissNotification(currentNotification.uid)}
          >
            Okay, thanks!
          </button>
        </div>
      </div>
    </Card>
  ) : null;
}
