import ReactDOM from 'react-dom';
import { useNavigate } from 'react-router-dom';
import { MainRoute } from '../types';
import WidgetProfile from './WidgetProfile';
import WidgetWallet from './WidgetWallet';
import WidgetCart from './WidgetCart';
import HeaderLogo from './HeaderLogo';
import SlackLink from './SlackLink';

export default function Header() {
  const navigate = useNavigate();

  const rootElement = document.getElementById('nccom-root');
  const rootParent = rootElement?.parentElement;
  let headerElement = document.querySelector('.aem-header');
  const aemHeader = document.querySelector('.header__grid');

  if (!aemHeader && !headerElement) {
    headerElement = document.createElement('div');
    headerElement.classList.add('aem-header');
    headerElement.setAttribute('data-cy', 'nccom-aem-header');
    headerElement.innerHTML = `<div class="aem-header__content">
      <div id="nccom-logo"></div>
      <div id="nccom-widget"></div>
    </div>`;
    rootParent?.insertBefore(headerElement, rootElement);
  }

  let logoElement = document.getElementById('nccom-logo');

  // testing dynamic portal for AEM integration
  if (!logoElement) {
    logoElement = document.createElement('div');
    logoElement.id = 'nccom-logo';
    if (aemHeader) {
      aemHeader.appendChild(logoElement);
    }
  }

  const renderLogo = logoElement
    ? ReactDOM.createPortal(<HeaderLogo />, logoElement)
    : null;

  let widgetElement = document.getElementById('nccom-widget');

  // testing dynamic portal for AEM integration
  if (!widgetElement) {
    widgetElement = document.createElement('div');
    widgetElement.id = 'nccom-widget';
    if (aemHeader) {
      aemHeader.appendChild(widgetElement);
    }
  }

  const renderWidget = widgetElement
    ? ReactDOM.createPortal(
        <div data-cy="nccom-widget" className="nc-widget">
          <SlackLink className="nc-widget__item" />
          <WidgetWallet />
          <WidgetCart onClick={() => navigate(MainRoute.Cart)} />
          <WidgetProfile />
        </div>,
        widgetElement
      )
    : null;

  return (
    <>
      {renderLogo}
      {renderWidget}
    </>
  );
}
