import React from 'react';
import { Link } from 'react-router-dom';
import { CustomerCartQuery } from '../graphql/schema';
import useVoucherSelection from '../hooks/useVoucherSelection';
import { MainRoute } from '../types';
import Card from './Card';
import Checkbox from './Checkbox';
import Divider from './Divider';
import LabelList from './LabelList';
import Typography from './Typography';

interface CheckoutSummaryPanelProps {
  customerCart: CustomerCartQuery['customerCart'];
  children: React.ReactNode;
}

export default function CartSummaryPanel({
  customerCart,
  children,
}: CheckoutSummaryPanelProps) {
  const { vouchers } = useVoucherSelection();

  const cartItemProductSkus =
    customerCart.items?.map((item) => item?.product.sku as string) || [];

  const hasVoucherForCartItems = vouchers.some((voucher) =>
    cartItemProductSkus.includes(voucher.sku as string)
  );

  const renderVoucherSelection = hasVoucherForCartItems ? (
    <>
      <Typography data-cy="nccom-cart-summary-voucher-yes" variant="h4">
        {vouchers.length > 1 ? 'Your vouchers' : 'Your voucher'}
      </Typography>
      {vouchers
        .filter((voucher) =>
          cartItemProductSkus.includes(voucher?.sku as string)
        )
        .map((voucher) => (
          <Checkbox
            data-cy="nccom-use-voucher-checkbox"
            key={voucher.code}
            id={`cart-use-voucher-${voucher.code}`}
            label={voucher.product_name}
            defaultChecked
            displayOnly
            // NOTE : as the MVP does not support voucher selection resp. other payment methods
            // we default check this control and remove the respective handler for the time being
            // checked={hasVoucher(voucher)}
            // onChange={() => handleVoucher(voucher)}
          />
        ))}
    </>
  ) : (
    <Typography data-cy="nccom-cart-summary-voucher-no">
      You have no vouchers for the items in your cart info text. Please visit
      bonus.ly to acquire the vouchers for your desired products.
    </Typography>
  );

  return (
    <>
      <Card variant="panel" className="nc-cart__summary__voucher">
        {vouchers.length ? (
          renderVoucherSelection
        ) : (
          <Typography>
            You currently have no vouchers. Please visit bonus.ly to acquire the
            vouchers for your desired products.
          </Typography>
        )}
      </Card>
      <div
        data-cy="nccom-cart-summary-invoice"
        className="nc-cart__summary__invoice"
      >
        <LabelList
          items={[
            {
              label: 'Subtotal',
              content: (
                <>
                  <strong>
                    {customerCart?.prices?.subtotal_including_tax?.value}
                  </strong>
                  &nbsp;
                  {customerCart?.prices?.subtotal_including_tax?.currency}
                </>
              ),
            },
            { label: 'Delivery', content: <strong>free</strong> },
            {
              label: 'Voucher',
              content: (
                <>
                  <strong>
                    {(customerCart?.prices?.subtotal_including_tax?.value ||
                      0) * -1}
                  </strong>
                  &nbsp;
                  {customerCart?.prices?.subtotal_including_tax?.currency}
                </>
              ),
            },
          ]}
        />
        <Divider color="primary" />
        <LabelList
          items={[
            {
              label: 'Total',
              content: (
                <>
                  <strong>0</strong>&nbsp;
                  {customerCart?.prices?.subtotal_including_tax?.currency}
                </>
              ),
            },
          ]}
        />
        <div className="nc-cart__summary__action"> {children}</div>
        <Typography
          data-cy="nccom-cart-summary-legal"
          className="nc-cart__summary__legal"
          variant="meta"
        >
          <Link to={`${MainRoute.Editorial}/terms-conditions`}>
            Return Policy
          </Link>
          : You have up to 7 working days to cancel your order. You can return
          it within 30 days from the date of receipt.
        </Typography>
      </div>
    </>
  );
}
