import clsx from 'clsx';
import React, { ButtonHTMLAttributes, ReactNode } from 'react';

interface IconButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  children: ReactNode;
  className?: string;
}

export default function IconButton({
  children,
  className,
  ...buttonProps
}: IconButtonProps) {
  return (
    <button
      className={clsx(className, 'nc-iconButton', {
        'nc-iconButton--isDisabled': buttonProps.disabled,
      })}
      {...buttonProps}
    >
      {children}
    </button>
  );
}
