import React, { useEffect, useLayoutEffect } from 'react';
import { useLocation, useRoutes } from 'react-router-dom';
import CartContextProvider from '../contexts/CartContextProvider';
import { useNotificationContext } from '../contexts/NotificationProvider';
import { useManualCountries } from '../graphql/queries';
import { useDispatch } from '../hooks';
import useCustomerLogin from '../hooks/useCustomerLogin';
import routes from '../routes';
import { setCountries } from '../store/ui';
import Divider from './Divider';
import NotificationHub from './NotificationHub';
import Skeleton from './Skeleton';
import Typography from './Typography';
import Header from './Header';

// styling
import '../fonts/Gellix-Light.css';
import '../fonts/Gellix-Regular.css';
import '../fonts/Gellix-SemiBold.css';
import '../styles.aem.scss';
import '../styles.scss';
import Footer from './Footer';

export default function App() {
  const { pushNotification } = useNotificationContext();
  const dispatch = useDispatch();

  const element = useRoutes(routes);
  const { pathname } = useLocation();

  // init customer
  const { loggedIn } = useCustomerLogin();

  // fill store with misc data
  const [getCountries] = useManualCountries();

  useEffect(() => {
    if (!loggedIn) return;

    getCountries().then(({ data, error }) => {
      if (error) {
        pushNotification({ apiError: error });
      }
      if (data) {
        dispatch(setCountries(data.countries));
      }
    });
  }, [dispatch, getCountries, loggedIn, pushNotification]);

  // scroll restoration
  useLayoutEffect(() => {
    window.scrollTo({
      top: 0,
      left: 0,
    });
  }, [pathname]);

  const renderApp = loggedIn ? (
    <CartContextProvider>
      <Header />
      {element}
    </CartContextProvider>
  ) : (
    <>
      <Divider />
      <Typography>Could not log you in</Typography>
    </>
  );

  return (
    <>
      <NotificationHub />
      <div className="main">
        {!loggedIn ? <Skeleton count={4} /> : renderApp}
      </div>
      <Footer />
    </>
  );
}
