import clsx from 'clsx';
import React, { SVGProps } from 'react';

interface SvgIconProps extends SVGProps<SVGSVGElement> {
  children: React.ReactNode;
  size?: 'none' | 'small' | 'medium';
}

export type IconProps = Omit<SvgIconProps, 'children'>;

export default function SvgIcon({
  children,
  className,
  size = 'small',
  viewBox = '0 0 24 24',
  focusable = false,
  role = 'img',
  ...other
}: SvgIconProps) {
  const props = {
    viewBox,
    focusable,
    role,
    ...other,
  };

  return (
    <svg
      className={clsx(className, 'nc-icon', `nc-icon--${size}`)}
      aria-hidden
      {...props}
    >
      {children}
    </svg>
  );
}
