import clsx from 'clsx';
import { useState } from 'react';
import { ProductDetailItem } from '../types';
import Image from './Image';

interface GalleryProps {
  mediaGallery: NonNullable<ProductDetailItem['media_gallery']>;
}

export default function Gallery({ mediaGallery }: GalleryProps) {
  const [selected, setSelected] = useState<number>(0);

  // TODO this is a workaround for the gallery layout just handling 3 images
  // -- content should be adapted
  // -- slider is to be implemented
  const shortGallery = mediaGallery.slice(0, 3);

  const handlePrev = () =>
    selected === 0
      ? setSelected(shortGallery.length - 1)
      : setSelected(selected - 1);
  const handleNext = () =>
    selected === shortGallery.length - 1
      ? setSelected(0)
      : setSelected(selected + 1);

  return (
    <div
      data-cy="nccom-gallery"
      className={clsx('nc-gallery', {
        'nc-gallery--no-thumbnails': shortGallery.length === 1,
      })}
    >
      <div data-cy="nccom-gallery-display" className="nc-gallery__display">
        <Image
          src={shortGallery[selected]?.url || ''}
          alt={shortGallery[selected]?.label || ''}
        />
        {shortGallery.length > 1 ? (
          <>
            <button
              data-cy="nccom-gallery-control-prev-button"
              className="nc-gallery__control nc-gallery__control--prev"
              onClick={handlePrev}
              aria-label="prev"
              type="button"
            />
            <button
              data-cy="nccom-gallery-control-next-button"
              className="nc-gallery__control nc-gallery__control--next"
              onClick={handleNext}
              aria-label="next"
              type="button"
            />
          </>
        ) : null}
      </div>

      {shortGallery.length > 1 ? (
        <div
          data-cy="nccom-gallery-thumbnails"
          className="nc-gallery__thumbnails"
        >
          {shortGallery.map((item, index) => (
            <Image
              key={item?.url}
              data-cy="nccom-gallery-thumbnail"
              className={clsx('nc-gallery__thumbnail', {
                'nc-gallery__thumbnail--isActive': index === selected,
              })}
              src={item?.url || ''}
              alt={item?.label || ''}
              onClick={() => setSelected(index)}
              withOverlay
            />
          ))}
        </div>
      ) : null}
    </div>
  );
}
