import React, {
  ReactNode,
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useState,
} from 'react';
import Button from '../components/Button';
import CustomerAddressDisplay from '../components/CustomerAddressDisplay';
import NotificationErrorIcon from '../components/icons/NotificationErrorIcon';
import Modal from '../components/Modal';
import Typography from '../components/Typography';
import {
  CreateCustomerAddressMutationString,
  DeleteCustomerAddressMutationString,
  UpdateCustomerAddressMutationString,
  useCreateCustomerAddress,
  useCustomerAddresses,
  useDeleteCustomerAddress,
  useUpdateCustomerAddress,
} from '../graphql/queries';
import { CustomerAddressInput } from '../graphql/schema';
import { useDispatch, useSelector } from '../hooks';
import useFetchOptionsOverrides from '../hooks/useFetchOptionsOverrides';
import {
  addCustomerAddress,
  removeCustomerAddress,
  setCustomerAddress,
  updateCustomerAddresses,
} from '../store/user';
import { AddressManagementContext, CustomerAddressData } from '../types';
import { useNotificationContext } from './NotificationProvider';

export const context = React.createContext<AddressManagementContext>({
  handleCreate: () => Promise.resolve(null),
  handleUpdate: () => Promise.resolve(),
  handleDelete: () => Promise.resolve(),
  busy: false,
});
export function useAddressManagementContext() {
  return useContext(context);
}

interface AddressManagementProviderProps {
  children: ReactNode;
}

export default function AddressManagementProvider({
  children,
}: AddressManagementProviderProps) {
  const dispatch = useDispatch();
  const { pushNotification } = useNotificationContext();
  const fetchOptionsOverrides = useFetchOptionsOverrides();

  const { customer } = useSelector(({ user }) => user);

  // create address
  const [createCustomerAddress, { loading: loadingCreateAddress }] =
    useCreateCustomerAddress();
  const handleCreate = useCallback(
    async (input: CustomerAddressInput) => {
      const { error, data: update } = await createCustomerAddress({
        fetchOptionsOverrides,
        variables: {
          input: {
            ...input,
            default_shipping: !customer?.addresses?.length,
            default_billing: !customer?.addresses?.length,
          },
        },
      });

      if (!error && update?.createCustomerAddress) {
        dispatch(addCustomerAddress(update.createCustomerAddress));
      } else {
        pushNotification({
          apiError: error,
        });
      }
      return update?.createCustomerAddress || null;
    },
    [
      createCustomerAddress,
      customer?.addresses?.length,
      dispatch,
      fetchOptionsOverrides,
      pushNotification,
    ]
  );

  // update address
  const [updateCustomerAddress, { loading: loadingUpdateAddress }] =
    useUpdateCustomerAddress();
  const handleUpdate = useCallback(
    async (id: CustomerAddressData['id'], input: CustomerAddressInput) => {
      const { error, data: update } = await updateCustomerAddress({
        fetchOptionsOverrides,
        variables: {
          id,
          input,
        },
      });

      if (!error && update?.updateCustomerAddress) {
        dispatch(setCustomerAddress(update.updateCustomerAddress));
      } else {
        pushNotification({
          apiError: error,
        });
      }
    },
    [dispatch, fetchOptionsOverrides, pushNotification, updateCustomerAddress]
  );

  // delete address
  // -- confirm delete id and address
  const [confirmDeleteId, setConfirmDeleteId] = useState<
    CustomerAddressData['id'] | null
  >(null);
  const deleteAddress = useMemo(
    () => customer?.addresses?.find((item) => item?.id === confirmDeleteId),
    [confirmDeleteId, customer?.addresses]
  );

  const [deleteCustomerAddress, { loading: loadingDeleteAddress }] =
    useDeleteCustomerAddress();
  const handleDelete = useCallback(
    async (id: CustomerAddressData['id']) => {
      const { error } = await deleteCustomerAddress({
        fetchOptionsOverrides,
        variables: {
          id,
        },
      });

      if (!error) {
        dispatch(removeCustomerAddress(id));
      } else {
        pushNotification({
          apiError: error,
        });
      }

      setConfirmDeleteId(null);
    },
    [deleteCustomerAddress, dispatch, fetchOptionsOverrides, pushNotification]
  );

  const { data: customerData, loading: loadingCustomerData } =
    useCustomerAddresses({
      fetchOptionsOverrides,
      refetchAfterMutations: [
        CreateCustomerAddressMutationString,
        UpdateCustomerAddressMutationString,
        DeleteCustomerAddressMutationString,
      ],
    });

  useEffect(() => {
    if (!customerData?.customer) return;

    dispatch(updateCustomerAddresses(customerData.customer));
  }, [customerData, dispatch]);

  const busy = useMemo(
    () =>
      loadingCreateAddress ||
      loadingUpdateAddress ||
      loadingDeleteAddress ||
      loadingCustomerData,
    [
      loadingCreateAddress,
      loadingCustomerData,
      loadingDeleteAddress,
      loadingUpdateAddress,
    ]
  );

  const contextValue = useMemo(
    () => ({
      handleCreate,
      handleUpdate,
      handleDelete: (id: CustomerAddressData['id']) => setConfirmDeleteId(id),
      busy,
    }),
    [handleCreate, handleUpdate, busy]
  );

  return (
    <context.Provider value={contextValue}>
      {children}
      {/* delete address confirmation modal */}
      <Modal
        isOpen={!!confirmDeleteId}
        onClose={() => setConfirmDeleteId(null)}
        disableClose={busy}
      >
        <Typography variant="h4">Delete Address</Typography>
        <Typography>The following address will be deleted</Typography>
        {deleteAddress && (
          <CustomerAddressDisplay
            className="nc-checkout__shipping__modalAddress"
            address={deleteAddress as CustomerAddressData}
          />
        )}
        <div className="nc-checkout__shipping__modalAction">
          <Button
            onClick={() => setConfirmDeleteId(null)}
            variant="secondary"
            disabled={busy}
          >
            Cancel
          </Button>
          <Button
            onClick={() => handleDelete(confirmDeleteId as number)}
            icon={<NotificationErrorIcon />}
            disabled={busy}
          >
            Delete
          </Button>
        </div>
      </Modal>
    </context.Provider>
  );
}
