import { useCallback, useEffect, useRef, useState } from 'react';

// TODO improve throttle to catch last execution
export default function useThrottle<A>(
  callback: (args: A) => void,
  timeout = 150
): [(args: A) => void, boolean] {
  const [ready, setReady] = useState(false);
  const timerRef = useRef<number | undefined>(undefined);

  const throttledCallback = useCallback<(args: A) => void>(
    (args) => {
      if (!ready) {
        return;
      }

      setReady(false);
      callback(args);
    },
    [ready, callback]
  );

  useEffect(() => {
    if (!ready) {
      timerRef.current = window.setTimeout(() => {
        setReady(true);
      }, timeout);
    }

    return () => window.clearTimeout(timerRef.current);
  }, [ready, timeout]);

  return [throttledCallback, ready];
}
