import { useEffect, useMemo, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { checkoutPaths } from '../routes';
import { CheckoutRoute, MainRoute } from '../types';

const getNextCheckoutRoute = (
  path: CheckoutRoute
): CheckoutRoute | undefined => {
  const currentIndex = checkoutPaths.indexOf(path);
  return checkoutPaths[currentIndex + 1];
};

export default function useCheckoutProcess() {
  const navigate = useNavigate();
  const location = useLocation();

  const [confirmed, setConfirmed] = useState<CheckoutRoute[]>([]);

  const confirmStep = (path: CheckoutRoute) => {
    if (!confirmed.includes(path)) {
      setConfirmed((state) => [...state, path]);
    }
    navigate(`${MainRoute.Checkout}/${getNextCheckoutRoute(path)}`);
  };

  const completed = useMemo(
    () =>
      confirmed.length === checkoutPaths.length - 1 &&
      confirmed.includes(CheckoutRoute.Confirmation),
    [confirmed]
  );

  const current = useMemo(
    () =>
      getNextCheckoutRoute(confirmed[confirmed.length - 1]) ||
      CheckoutRoute.Summary,
    [confirmed]
  );

  const [finaleWasSeen, setFinaleWasSeen] = useState(false);

  // validate if is checkout route and redirect or reset checkout process if necessary
  useEffect(() => {
    const resetCheckout = () => {
      setConfirmed([]);
      setFinaleWasSeen(false);
    };

    if (!location.pathname.includes(MainRoute.Checkout)) {
      if (confirmed.length !== 0 && !finaleWasSeen) {
        resetCheckout();
      }
      return;
    }

    const locationPath = location.pathname.replace(
      `${MainRoute.Checkout}/`,
      ''
    ) as CheckoutRoute;

    // register navigate flow and persist if finale was seen
    if (
      completed &&
      current === locationPath &&
      current === CheckoutRoute.Finale
    ) {
      setFinaleWasSeen(true);
    }

    // reset state if completed and route changes
    if (completed && finaleWasSeen && current !== locationPath) {
      resetCheckout();
      navigate(`${MainRoute.Checkout}/${CheckoutRoute.Summary}`);
      return;
    }

    // redirect if locationPath not confirmed
    if (
      !completed &&
      !confirmed.includes(locationPath) &&
      current !== locationPath
    ) {
      navigate(`${MainRoute.Checkout}/${current}`);
    }
  }, [
    completed,
    confirmed,
    current,
    finaleWasSeen,
    location.pathname,
    navigate,
  ]);

  return {
    confirmStep,
    confirmed,
    current,
    completed,
  };
}
