import { useConfigurationContext } from '../contexts/ConfigurationProvider';
import { useSelector } from '../hooks';
import Button from './Button';
import Skeleton from './Skeleton';
import Typography from './Typography';

export default function Stage() {
  const { title, description, loading } = useSelector(
    (state) => state.ui.stage
  );

  const config = useConfigurationContext();

  return (
    <header data-cy="nccom-stage" className="nc-stage">
      <div data-cy="nccom-stage-action" className="nc-stage__action">
        <Button
          data-cy="nccom-get-voucher-button"
          className="nc-stage__action__button"
          variant="secondary"
          href={config.bonusly}
          target="blank"
        >
          Pay with Bonusly
        </Button>
      </div>
      <div data-cy="nccom-stage-header" className="nc-stage__header">
        {loading ? (
          <Skeleton variant="stage" />
        ) : (
          <Typography
            data-cy="nccom-stage-header-title"
            variant="h2"
            element="h1"
          >
            {title}
          </Typography>
        )}
        {loading
          ? null
          : description && (
              <Typography
                data-cy="nccom-stage-header-description"
                className="nc-stage__header__description"
              >
                {description}
              </Typography>
            )}
      </div>
    </header>
  );
}
