import { useCallback } from 'react';
import { useCartContext } from '../contexts/CartContextProvider';
import { useNotificationContext } from '../contexts/NotificationProvider';
import {
  useApplyCouponToCart,
  useRemoveCouponFromCart,
} from '../graphql/queries';
import useFetchOptionsOverrides from './useFetchOptionsOverrides';
import useVoucherSelection from './useVoucherSelection';

export default function useUpdateCouponCode() {
  const fetchOptionsOverrides = useFetchOptionsOverrides();
  const { pushNotification } = useNotificationContext();

  const { cart } = useCartContext();
  const { couponCode } = useVoucherSelection();

  const [removeCouponFromCart] = useRemoveCouponFromCart();
  const [applyCouponToCart] = useApplyCouponToCart();

  const updateCouponCode = useCallback(async () => {
    if (!cart?.id) return;

    const remove = await removeCouponFromCart({
      fetchOptionsOverrides,
      variables: {
        cartId: cart.id as string,
      },
    });

    if (remove.error) {
      pushNotification({ apiError: remove.error });
    }

    if (!couponCode) return;

    const apply = await applyCouponToCart({
      fetchOptionsOverrides,
      variables: {
        cartId: cart.id as string,
        couponCode,
      },
    });

    if (apply.error) {
      pushNotification({ apiError: apply.error });
    }
  }, [
    applyCouponToCart,
    cart?.id,
    couponCode,
    fetchOptionsOverrides,
    pushNotification,
    removeCouponFromCart,
  ]);

  return {
    updateCouponCode,
  };
}
