import clsx from 'clsx';
import React, {
  ChangeEvent,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import useClickAwayListener from '../hooks/useClickAwayListener';
import { FormControlProps, Option } from '../types';
import Card from './Card';
import ChevronDownIcon from './icons/ChevronDownIcon';
import Typography from './Typography';

interface SelectProps extends FormControlProps {
  options: Option[];
  id?: string;
  value?: Option['value'];
  onChange?: (option: Option) => void;
  onBlur?: () => void;
  className?: string;
  placeholder?: string;
  disabled?: boolean;
  withSearch?: boolean;
  dataCyOverride?: string;
}

/* eslint-disable react/prop-types */
const Select = React.forwardRef<HTMLButtonElement, SelectProps>(
  (props, ref) => {
    const {
      id,
      label,
      helperText,
      placeholder,
      options,
      value,
      onChange,
      onBlur,
      className,
      error,
      disabled,
      withSearch = false,
      dataCyOverride,
    } = props;

    const selectRef = useRef<HTMLDivElement | null>(null);
    const inputRef = useRef<HTMLInputElement | null>(null);
    const searchRef = useRef<HTMLInputElement | null>(null);

    const [open, setOpen] = useState(false);
    useClickAwayListener({
      onClickAway: () => {
        if (open) {
          setOpen(false);
        }
      },
      containerRef: selectRef,
    });

    const selected = useMemo(
      () => options.find((option) => option.value === value),
      [value, options]
    );

    const handleChange = (
      nextOption: Option,
      event: React.MouseEvent<HTMLButtonElement>
    ) => {
      event.stopPropagation();

      if (onChange) {
        onChange(nextOption);
      }
      if (onBlur) {
        onBlur();
      }
      setOpen(false);

      const ev = new Event('change', { bubbles: true });
      inputRef.current?.dispatchEvent(ev);
    };

    const [search, setSearch] = useState('');
    const handleSearch = (event: ChangeEvent<HTMLInputElement>) => {
      event.stopPropagation();
      setSearch(event.target.value);
    };

    const handleToggle = () => {
      if (disabled) return;
      setOpen((state) => !state);
    };
    useEffect(() => {
      if (open && searchRef.current) {
        searchRef.current?.focus();
      } else {
        setSearch('');
      }
    }, [open, withSearch]);

    const filteredOptions = useMemo<SelectProps['options']>(
      () =>
        withSearch && search
          ? options.filter((item) => {
              const regEx = new RegExp(search, 'i');
              return item.label ? regEx.test(item.label) : false;
            })
          : options,
      [options, search, withSearch]
    );

    return (
      <div
        id={id}
        className={clsx(className, 'nc-select', 'nc-formControl', {
          'nc-formControl--hasError': error,
          'nc-formControl--isDisabled': disabled,
        })}
        ref={(node) => {
          selectRef.current = node;
        }}
      >
        <input
          ref={inputRef}
          className="nc-select__input"
          type="text"
          defaultValue={selected?.value || ''}
          tabIndex={-1}
        />
        {label && (
          <div
            data-cy={`nccom-form-control-${label
              .replaceAll(' ', '-')
              .replaceAll(/[*]/g, '')
              .toLowerCase()}-label`}
            className="nc-formControl__label"
          >
            {label}
          </div>
        )}
        <div className="nc-select__control">
          <button
            ref={ref}
            data-cy={
              `nccom-select-${label
                ?.replaceAll(' ', '-')
                .replaceAll(/[*]/g, '')
                .toLowerCase()}-toggle` || dataCyOverride
            }
            className={clsx('nc-select__toggle', {
              'nc-select__toggle--isOpen': open,
            })}
            onClick={handleToggle}
            onBlur={onBlur ? () => onBlur() : undefined}
            type="button"
          >
            {withSearch && open ? (
              <input
                className="nc-select__search"
                ref={searchRef}
                type="text"
                placeholder="Search"
                onChange={handleSearch}
              />
            ) : (
              <span
                data-cy="nccom-select-toggle-label"
                className="nc-select__toggle__label"
              >
                {disabled
                  ? value
                  : selected?.label ||
                    selected?.value ||
                    placeholder ||
                    '-- error --'}
              </span>
            )}
            <ChevronDownIcon className="nc-select__toggle__icon" />
          </button>
          {open ? (
            <Card className="nc-select__options">
              {withSearch && !filteredOptions.length && (
                <div className="nc-select__option">No results</div>
              )}
              {filteredOptions.map((option) => (
                <button
                  data-cy={`nccom-select-option-${option.label
                    ?.toLowerCase()
                    .replaceAll(' ', '-')
                    .replaceAll('&', 'and')}`}
                  key={option.value + (option.label || '')}
                  className={clsx('nc-select__option', {
                    'nc-select__option--isSelected':
                      value && option.value === value,
                    'nc-select__option--isDisabled': option.disabled,
                  })}
                  onClick={
                    !option.disabled
                      ? (event) => handleChange(option, event)
                      : undefined
                  }
                >
                  {option.label || option.value}
                </button>
              ))}
            </Card>
          ) : null}
        </div>
        {helperText && (
          <Typography className="nc-formControl__helperText" variant="meta">
            {helperText}
          </Typography>
        )}
      </div>
    );
  }
);

Select.displayName = 'Select';

export default Select;
