import { useState } from 'react';
import { useConfigurationContext } from '../contexts/ConfigurationProvider';
import { ConfigurableProductOptions } from '../graphql/schema';
import useProductOptionsSelection from '../hooks/useProductOptionsSelection';
import Button from './Button';
import WalletIcon from './icons/WalletIcon';
import Modal from './Modal';
import ProductAddToCartButton, {
  ProductAddToCartButtonProps,
} from './ProductAddToCartButton';
import ProductOptionSelector from './ProductOptionSelector';
import Typography from './Typography';

interface ProductAddToCartDialogProps
  extends Omit<ProductAddToCartButtonProps, 'options'> {
  options: ConfigurableProductOptions[];
}

export default function ProductAddToCartDialog({
  sku,
  voucherCode,
  options,
  label,
  ...buttonProps
}: ProductAddToCartDialogProps) {
  const config = useConfigurationContext();

  const { selectedOptions, setSelectedOptions, selectionComplete } =
    useProductOptionsSelection(options);

  const [modalOpen, setModalOpen] = useState(false);

  return (
    <>
      <Button
        data-cy="nccom-add-to-cart-button"
        onClick={() => setModalOpen(true)}
        icon={voucherCode ? <WalletIcon /> : undefined}
        disabled={!voucherCode}
        {...buttonProps}
      >
        {label || config.productListItem.orderButtonLabel}
      </Button>
      <Modal
        className="nc-productAddToCartDialog__modal"
        isOpen={modalOpen}
        onClose={() => setModalOpen(false)}
      >
        <Typography>Please select from the following options</Typography>
        {/* TODO selected options are sure in this context but must be optional for product detail
               -- find a way to avoid this condition */}
        {selectedOptions && (
          <ProductOptionSelector
            className="nc-productAddToCartDialog__selector"
            options={options}
            selectedOptions={selectedOptions}
            onChange={(update) => setSelectedOptions(update)}
          />
        )}
        <ProductAddToCartButton
          data-cy="nccom-modal-add-product-to-cart"
          className="nc-productAddToCartDialog__action"
          sku={sku}
          options={selectedOptions}
          voucherCode={voucherCode}
          disabled={!selectionComplete}
          label={label}
          fullWidth
        />
      </Modal>
    </>
  );
}
