import React from 'react';

interface HtmlDisplayProps {
  content: string;
}

export default function HtmlDisplay({ content }: HtmlDisplayProps) {
  const handler = (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
    const target = event?.target as HTMLAnchorElement;
    if ((target as HTMLAnchorElement)?.hash?.startsWith('#')) {
      event?.preventDefault();
      const element = document.getElementById(target?.hash?.replace('#', ''));
      element?.scrollIntoView({
        behavior: 'smooth',
        block: 'start',
      });
    }
  };

  return (
    // eslint-disable-next-line jsx-a11y/click-events-have-key-events, jsx-a11y/no-static-element-interactions
    <div
      onClick={handler}
      className="nc-htmlDisplay"
      // eslint-disable-next-line react/no-danger
      dangerouslySetInnerHTML={{
        __html: content,
      }}
    />
  );
}
