import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import {
  CreateCustomerAddressMutation,
  CustomerAddress,
  CustomerAddressesQuery,
  CustomerCartIdQuery,
  CustomerQuery,
  Maybe,
  UpdateCustomerAddressMutation,
  Voucher,
} from '../graphql/schema';

// Define a type for the slice state
export interface UserState {
  customer: CustomerQuery['customer'];
  cartId: null | CustomerCartIdQuery['customerCart']['id'];
  cartTotalQuantity:
    | null
    | CustomerCartIdQuery['customerCart']['total_quantity'];
  token: string | null;
  vouchers: Voucher[];
}

// Define the initial state using that type
const initialState: UserState = {
  customer: null,
  cartId: null,
  cartTotalQuantity: null,
  token: null,
  vouchers: [],
};

export const { reducer, actions } = createSlice({
  name: 'user',
  // `createSlice` will infer the state type from the `initialState` argument
  initialState,
  /* eslint-disable no-param-reassign */
  reducers: {
    setToken: (state, { payload }: PayloadAction<UserState['token']>) => {
      state.token = payload;
    },
    setCustomer: (state, { payload }: PayloadAction<UserState['customer']>) => {
      state.customer = payload;
    },
    setCustomerAddress: (
      state,
      {
        payload,
      }: PayloadAction<
        NonNullable<UpdateCustomerAddressMutation['updateCustomerAddress']>
      >
    ) => {
      const { addresses, ...customer } = state.customer as NonNullable<
        UserState['customer']
      >;

      state.customer = {
        ...customer,
        addresses:
          addresses?.map((address) =>
            address?.id === payload.id ? payload : address
          ) || [],
      };
    },
    removeCustomerAddress: (
      state,
      { payload }: PayloadAction<CustomerAddress['id']>
    ) => {
      const { addresses, ...customer } = state.customer as NonNullable<
        UserState['customer']
      >;
      state.customer = {
        ...customer,
        addresses:
          addresses?.reduce<CustomerAddress[]>(
            (acc, address) =>
              address?.id === payload
                ? acc
                : [...acc, address as CustomerAddress],
            []
          ) || [],
      };
    },
    addCustomerAddress: (
      state,
      {
        payload,
      }: PayloadAction<
        NonNullable<CreateCustomerAddressMutation['createCustomerAddress']>
      >
    ) => {
      const { addresses, ...customer } = state.customer as NonNullable<
        UserState['customer']
      >;
      state.customer = {
        ...customer,
        addresses: [...(addresses || []), payload],
      };
    },
    updateCustomerAddresses: (
      state,
      {
        payload,
      }: PayloadAction<NonNullable<CustomerAddressesQuery['customer']>>
    ) => {
      const { addresses, ...customer } = state.customer as NonNullable<
        UserState['customer']
      >;

      const addressesUpdate =
        addresses?.map<NonNullable<CustomerAddress>>((address) => ({
          ...(address as CustomerAddress),
          default_billing: payload.addresses?.find(
            (item) => item?.id === address?.id
          )?.default_billing as Maybe<boolean>,
          default_shipping: payload.addresses?.find(
            (item) => item?.id === address?.id
          )?.default_shipping as Maybe<boolean>,
        })) || [];

      state.customer = {
        ...customer,
        default_billing: payload.default_billing,
        default_shipping: payload.default_shipping,
        addresses: addressesUpdate,
      };
    },
    setCustomerCartId: (
      state,
      { payload }: PayloadAction<UserState['cartId']>
    ) => {
      state.cartId = payload;
    },
    setVouchers: (state, { payload }: PayloadAction<UserState['vouchers']>) => {
      state.vouchers = payload;
    },
  },
  /* eslint-enable no-param-reassign */
});

// eslint-disable-next-line no-empty-pattern
export const {
  setToken,
  setCustomer,
  setCustomerAddress,
  removeCustomerAddress,
  addCustomerAddress,
  updateCustomerAddresses,
  setCustomerCartId,
  setVouchers,
} = actions;

export default reducer;
