interface WidgetCounterProps {
  count: number;
  dataCy: string;
};

export default function WidgeCounter({count, dataCy}: WidgetCounterProps) {

  return (
    (count > 0) ?
      <span data-cy={dataCy} className="nc-widget__counter">
        <span className="nc-widget__counter__label">{count}</span>
      </span>
    : null
  )
}