import clsx from 'clsx';
import React, {
  CSSProperties,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import { Transition } from 'react-transition-group';
import useWindowSize from '../hooks/useWindowSize';
import ChevronDown from './icons/ChevronDownIcon';
import Typography from './Typography';

// TODO
// - accessibility for handler

interface AccordionProps {
  label: string;
  children: React.ReactNode;
  isOpen?: boolean;
  duration?: number;
}

export default function Accordion({
  label,
  children,
  isOpen: initialIsOpen = false,
  duration = 360,
}: AccordionProps) {
  const { width } = useWindowSize();

  const transitionNode = useRef<HTMLDivElement>(null);

  const [isOpen, setIsOpen] = useState(initialIsOpen);
  useEffect(() => {
    setIsOpen(initialIsOpen);
  }, [initialIsOpen]);

  // handle tabindeces for invisible elements to avoid focus of closed accordion content
  useEffect(() => {
    if (transitionNode.current) {
      const focusableElements =
        transitionNode.current.querySelectorAll<HTMLElement>(
          'button, input, textarea, select, a, link, [tabindex]'
        );

      /* eslint-disable no-param-reassign */
      focusableElements.forEach((element) => {
        const { tabIndex } = element;
        const { originalTabIndex } = element.dataset;

        if (tabIndex === -1 && !originalTabIndex) return;

        if (tabIndex != null && !originalTabIndex) {
          element.dataset.originalTabIndex = tabIndex.toString();
        }

        if (isOpen) {
          if (originalTabIndex) {
            element.tabIndex = Number(element.dataset.originalTabIndex);
          } else {
            element.removeAttribute('tabindex');
          }
        } else {
          element.tabIndex = -1;
        }
      });
      /* eslint-enable no-param-reassign */
    }
  }, [isOpen]);

  const [contentHeight, setContentHeight] = useState(0);

  const handleContentHeight = useCallback(
    (node) => {
      if (node) {
        setContentHeight(node.clientHeight);
      }
    },
    // in order to invalidate the content height on window resize
    // we pass the window width as a unused dependency
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [width]
  );

  const transitionStyles: { [index: string]: CSSProperties } = useMemo(
    () => ({
      entering: {
        height: contentHeight,
      },
      entered: {
        height: contentHeight,
      },
      exiting: {
        height: 0,
      },
    }),
    [contentHeight]
  );

  return (
    <section
      data-cy="nccom-accordion"
      className={clsx('nc-accordion', {
        'nc-accordion--isOpen': isOpen,
      })}
    >
      {/* eslint-disable-next-line jsx-a11y/no-static-element-interactions,jsx-a11y/click-events-have-key-events */}
      <header
        data-cy="nccom-accordion-header"
        className="nc-accordion__header"
        onClick={() => setIsOpen(!isOpen)}
      >
        <Typography
          variant="h4"
          element="span"
          data-cy="nccom-accordion-label"
          className="nc-accordion__label"
        >
          {label}
        </Typography>
        <ChevronDown
          data-cy="nccom-accordion-icon"
          className="nc-accordion__icon"
        />
      </header>
      <Transition in={isOpen} timeout={duration} nodeRef={transitionNode}>
        {(status) => (
          <div
            ref={transitionNode}
            data-cy="nccom-accordion-content"
            className="nc-accordion__content"
            style={{
              transitionDuration: `${duration}ms`,
              ...transitionStyles[status],
            }}
          >
            <div
              ref={handleContentHeight}
              data-cy="nccom-accordion-content-inner"
              className="nc-accordion__content__inner"
            >
              {children}
            </div>
          </div>
        )}
      </Transition>
    </section>
  );
}
