import React, { useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useCartContext } from '../contexts/CartContextProvider';
import { useNotificationContext } from '../contexts/NotificationProvider';
import { CustomerCartItem, MainRoute } from '../types';
import Button from './Button';
import Card from './Card';
import CartItem from './CartItem';
import CartSummaryPanel from './CartSummaryPanel';
import DeliveryIcon from './icons/DeliveryIcon';
import Skeleton from './Skeleton';
import Typography from './Typography';

export default function Cart() {
  const navigate = useNavigate();
  const { pushNotification } = useNotificationContext();

  const { cart, loading, error } = useCartContext();

  useEffect(() => {
    if (error) {
      pushNotification({
        apiError: error,
      });
    }
  }, [error, pushNotification]);

  const renderCart = () =>
    cart.total_quantity ? (
      <div className="nc-checkout">
        <div
          data-cy="nccom-checkout-main-summary"
          className="nc-checkout__main nc-checkout__main--summary"
        >
          <Card>
            {cart.items?.map((cartItem) => (
              <CartItem
                key={cartItem?.uid}
                cartItem={cartItem as CustomerCartItem}
              />
            ))}
          </Card>
          <div
            data-cy="nccom-cart-list-footer"
            className="nc-cart__list__footer"
          >
            <Typography>Items are reserved for 72 hours</Typography>
            <Typography startAdornment={<DeliveryIcon />}>
              Free of charge – standard delivery
            </Typography>
          </div>
        </div>
        <Card className="nc-checkout__aside">
          {cart && (
            <CartSummaryPanel customerCart={cart}>
              <Button onClick={() => navigate(MainRoute.Checkout)} fullWidth>
                Checkout
              </Button>
            </CartSummaryPanel>
          )}
        </Card>
      </div>
    ) : (
      <div className="nc-checkout">
        <div
          data-cy="nccom-checkout-summary-empty"
          className="nc-checkout__main nc-checkout__main--summary"
        >
          <Typography variant="h4">There are no items in your cart</Typography>
          <Typography autoMargin>
            <Link data-cy="nccom-cart-link" to={MainRoute.Product}>
              Go to products page.
            </Link>
          </Typography>
        </div>
      </div>
    );

  return loading ? <Skeleton count={3} /> : renderCart();
}
