import React, { useContext } from 'react';
import { useNotificationContext } from '../contexts/NotificationProvider';
import { useSetShippingAddress } from '../graphql/queries';
import { useSelector } from '../hooks';
import useCreateCheckoutAddress from '../hooks/useCreateCheckoutAddress';
import useFetchOptionsOverrides from '../hooks/useFetchOptionsOverrides';
import { CheckoutRoute, CustomerAddressData } from '../types';
import Button from './Button';
import Card from './Card';
import CartSummaryPanel from './CartSummaryPanel';
import { checkoutContext } from './Checkout';
import CheckoutSection from './CheckoutSection';
import CustomerAddressForm from './CustomerAddressForm';
import CustomerAddressSelect from './CustomerAddressSelect';
import Skeleton from './Skeleton';
import Typography from './Typography';

export default function CheckoutShipping() {
  const { pushNotification } = useNotificationContext();

  const {
    customerCart,
    confirmStep,
    contextData: { shippingAddressId },
    setShippingAddressId,
  } = useContext(checkoutContext);

  const fetchOptionsOverrides = useFetchOptionsOverrides();
  const { cartId, customer } = useSelector(({ user }) => user);

  // address creation
  const { createAddress, setCreateAddress, handleCreateCustomerAddress, busy } =
    useCreateCheckoutAddress(setShippingAddressId);

  // cart shipping & shipping method
  const [setShippingAddress, { loading }] = useSetShippingAddress();

  // TODO separate?: handles shipping address as well as the shipping method
  // TODO establish strategy for shipping method (config or user selection?)
  const handleSetShippingAddress = async () => {
    if (!shippingAddressId) return;

    const { error } = await setShippingAddress({
      fetchOptionsOverrides,
      variables: {
        cartId: cartId as string,
        shippingId: shippingAddressId as number, // possible NaN ?
        shippingMethods: [
          { method_code: 'freeshipping', carrier_code: 'freeshipping' },
        ],
      },
    });

    if (!error) {
      confirmStep(CheckoutRoute.Shipping);
    } else {
      pushNotification({ apiError: error });
    }
  };

  return (
    <div className="nc-checkout">
      {loading ? (
        <Skeleton variant="checkout" />
      ) : (
        <>
          <Card className="nc-checkout__main">
            <CheckoutSection header="Email Address">
              <Typography>{customer?.email}</Typography>
            </CheckoutSection>
            <CheckoutSection header="Delivery Address" dense={!createAddress}>
              {!customer?.addresses?.length || createAddress ? (
                <CustomerAddressForm
                  className="nc-checkout__shipping__addressForm"
                  address={{
                    firstname: customer?.firstname as string,
                    lastname: customer?.lastname as string,
                  }}
                  loading={busy}
                  onSubmit={handleCreateCustomerAddress}
                  onCancel={() => setCreateAddress(false)}
                  hideCancel={!customer?.addresses?.length}
                />
              ) : (
                <CustomerAddressSelect
                  addresses={customer?.addresses as CustomerAddressData[]}
                  value={shippingAddressId as number}
                  onChange={(id) => setShippingAddressId(id)}
                  displayDefaults="shipping"
                />
              )}
              {customer?.addresses?.length && !createAddress ? (
                <Button
                  onClick={() => setCreateAddress(true)}
                  variant="secondary"
                >
                  Add a new address
                </Button>
              ) : null}
            </CheckoutSection>
          </Card>
          <Card className="nc-checkout__aside">
            {customerCart && (
              <CartSummaryPanel customerCart={customerCart}>
                <Button
                  onClick={handleSetShippingAddress}
                  disabled={!shippingAddressId || loading || busy}
                  fullWidth
                >
                  Continue
                </Button>
              </CartSummaryPanel>
            )}
          </Card>
        </>
      )}
    </div>
  );
}
