import { APIError } from 'graphql-hooks';
import React from 'react';
import {
  BillingCartAddress,
  CmsPageQuery,
  ConfigurableCartItem,
  ConfigurableProduct,
  CountryCodeEnum,
  CreateCustomerAddressMutation,
  CustomerAddressInput,
  CustomerCartQuery,
  CustomerQuery,
  ProductDetailQuery,
  ProductListQuery,
  Region,
  ShippingCartAddress,
  Voucher,
} from './graphql/schema';
import store from './store';

// Redux Toolkit Store
// Infer the `RootState` and `AppDispatch` types from the store itself
export type RootState = ReturnType<typeof store.getState>;
// Inferred type: {posts: PostsState, comments: CommentsState, users: UsersState}
export type AppDispatch = typeof store.dispatch;

// Graph QL
export interface GraphQlError {
  message: string;
  [index: string]: unknown;
}

export type ProductConfigurableOptions = Pick<
  NonNullable<ConfigurableProduct>,
  'configurable_options'
>;

// TODO improve the dynamic typing in favor of making `configurable_options` manually an optional field
export type ProductItem = NonNullable<
  NonNullable<NonNullable<ProductListQuery['products']>['items']>[number]
> &
  ProductConfigurableOptions;

// TODO improve the dynamic typing in favor of making `configurable_options` manually an optional field
export type ProductDetailItem = NonNullable<
  NonNullable<NonNullable<ProductDetailQuery['products']>['items']>[number]
> &
  ProductConfigurableOptions;

export type CustomerCartItem = NonNullable<
  NonNullable<NonNullable<CustomerCartQuery['customerCart']>['items']>[number]
> &
  Pick<NonNullable<ConfigurableCartItem>, 'configurable_options'> &
  Pick<NonNullable<ConfigurableCartItem>, 'configured_variant'>;

export type ProductSelectedOptions = Record<
  NonNullable<
    NonNullable<CustomerCartItem['configurable_options']>[number]
  >['configurable_product_option_uid'],
  NonNullable<
    NonNullable<CustomerCartItem['configurable_options']>[number]
  >['configurable_product_option_value_uid']
>;

export type CustomerAddressData = NonNullable<
  NonNullable<NonNullable<CustomerQuery['customer']>['addresses']>[number]
> & { id: number };

export type CustomerAddressFormData = {
  firstname: string;
  lastname: string;
  street: string;
  telephone: string;
  postcode: string;
  city: string;
  region: Region['id'] | string;
  country_code: CountryCodeEnum;
};

export type AddressDisplayInput =
  | CustomerAddressData
  | ShippingCartAddress
  | BillingCartAddress;

export type AddressDisplayData = {
  firstname: string;
  lastname: string;
  street: (string | null)[];
  telephone: string;
  postcode: string;
  city: string;
  region?: string;
  countryCode: CountryCodeEnum | string;
  countryName: string;
  default_shipping?: boolean | null;
  default_billing?: boolean | null;
};

export type CustomerAddressFields = keyof CustomerAddressFormData;
export type CustomerAddressSelectFields = keyof Pick<
  CustomerAddressFormData,
  'country_code' | 'region'
>;

export type RegionMap = Partial<Record<CountryCodeEnum, Region[]>>;

// UI
export enum MainRoute {
  Product = '/product',
  Checkout = '/checkout',
  Cart = '/cart',
  Account = '/account',
  Editorial = '/editorial',
}

export enum CheckoutRoute {
  Summary = 'summary',
  Shipping = 'shipping',
  Payment = 'payment',
  Confirmation = 'confirmation',
  Finale = 'finale',
}

export enum AccountSection {
  OrderHistory = 'orders',
  AddressBook = 'addresses',
}

export type ProductRouteParams = 'productSku';

export type EditorialRouteParams = 'identifier';

export interface StageContent {
  title: string;
  description?: string;
  loading?: boolean;
}
export interface StageContents {
  [index: string]: StageContent;
}

export interface Option {
  value: string | number | null;
  label?: string;
  disabled?: boolean;
}

export interface LabelListItem {
  label: string;
  content: React.ReactText | React.ReactNode;
}

export interface CartContext {
  cart: CustomerCartQuery['customerCart'];
  getCartItemByProductSku: (sku: ProductItem['sku']) => CustomerCartItem | null;
  loading: boolean;
  confirmStep: (path: CheckoutRoute) => void;
  confirmed: CheckoutRoute[];
  completed: boolean;
  current: CheckoutRoute;
  error: APIError<GraphQlError> | null;
}

export interface CheckoutContextObjectStore {
  shippingAddressId: number | null;
  billingAddressId: number | null;
  billingIsShipping: boolean | null;
  [index: string]: string | number | null | boolean;
}

export interface CheckoutContext {
  customerCart: CustomerCartQuery['customerCart'] | null;
  confirmStep: (stepIndex: CheckoutRoute) => void;
  // simple object store
  contextData: CheckoutContextObjectStore;
  setShippingAddressId: (id: CustomerAddressData['id']) => void;
  setBillingAddressId: (id: CustomerAddressData['id']) => void;
  setBillingIsShipping: () => void;
  loading: boolean;
}

export interface CheckoutStep {
  index: number;
  label: string;
  confirmed: boolean;
  current: boolean;
  path?: CheckoutRoute;
}

export interface FormControlProps {
  label?: string;
  helperText?: string;
  error?: boolean;
  className?: string;
}

export interface AddressManagementContext {
  handleCreate: (
    input: CustomerAddressInput
  ) => Promise<CreateCustomerAddressMutation['createCustomerAddress'] | null>;
  handleUpdate: (
    id: CustomerAddressData['id'],
    input: CustomerAddressInput
  ) => Promise<void>;
  handleDelete: (id: CustomerAddressData['id']) => void;
  busy: boolean;
}

export interface Notification {
  uid: string;
  type?: 'default' | 'error' | 'success' | 'warning';
  message: string;
  date: Date;
  autoDismiss?: boolean;
}

export interface PaginationMeta {
  total: number;
  from: number;
  to: number;
}

export interface ProductVoucher {
  [index: string]: {
    productName: Voucher['product_name'];
    all: Voucher['code'][];
    available?: Voucher['code'][];
    applied?: Voucher['code'][];
  };
}

export type EditorialPage = NonNullable<
  NonNullable<NonNullable<CmsPageQuery['cmsPage']>['content']>[number]
>;

export type SocialPlatformType = 'instagram' | 'facebook' | 'twitter';

export type SocialItem = {
  type: SocialPlatformType;
  link: string;
};
