import { useEffect, useState } from 'react';
import { useKeycloak } from '@react-keycloak/web';
import { useConfigurationContext } from '../contexts/ConfigurationProvider';
import { useNotificationContext } from '../contexts/NotificationProvider';
import { useManualCustomer } from '../graphql/queries';
import { Voucher } from '../graphql/schema';
import { setCustomer, setToken, setVouchers } from '../store/user';
import { useDispatch, useSelector } from './index';
import useFetchOptionsOverrides from './useFetchOptionsOverrides';

/**
 * This hook
 * - retrieves the customer token (provisionally queried directly from magento)
 * - retrieves the customer data
 */
export default function useCustomerLogin() {
  const { keycloak, initialized } = useKeycloak();

  // authenticate keycloak
  useEffect(() => {
    if (initialized && !keycloak?.authenticated) {
      keycloak?.login();
    }
  }, [initialized, keycloak, keycloak?.authenticated]);

  const { pushNotification } = useNotificationContext();
  const dispatch = useDispatch();
  const { magento } = useConfigurationContext();
  const fetchOptionsOverrides = useFetchOptionsOverrides();
  const { token, customer } = useSelector(({ user }) => user);

  const [loading, setLoading] = useState(true);

  // get magento customer token
  useEffect(() => {
    if (!keycloak?.idToken || token) return;

    fetch(`${magento.url}/rest/V1/integration/customer/token`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: keycloak.idToken,
      },
      body: JSON.stringify({
        username: 'null', // just send something here to not trigger Magento's validation for empty request parameters
        password: 'null', // has no other effect because the idToken sent in the Authorization header is used for signup / signin instead
      }),
    })
      .then((response) => (response.ok ? response.json() : null))
      .then((tokenOrNull) => {
        dispatch(setToken(tokenOrNull));
        setLoading(false);
      })
      .catch(() => {
        pushNotification({
          type: 'error',
          message: 'No token available',
        });
      });
  }, [keycloak?.idToken, dispatch, magento.url, pushNotification, token]);

  // get magento customer
  const [getCustomer, { loading: loadingCustomer = true }] = useManualCustomer({
    fetchOptionsOverrides,
  });

  useEffect(() => {
    if (!token) return;
    getCustomer().then(({ data, error }) => {
      if (error) {
        pushNotification({ apiError: error });
      }
      if (!data) return;
      dispatch(setCustomer(data.customer));
      dispatch(setVouchers((data.customer?.vouchers as Voucher[]) || []));
    });
  }, [dispatch, getCustomer, pushNotification, token]);

  return {
    loading: !initialized || loading || loadingCustomer,
    loggedIn: !!keycloak?.idToken && !!token && !!customer?.email,
  };
}
