import clsx from 'clsx';
import React, { ForwardedRef, InputHTMLAttributes } from 'react';
import { FormControlProps } from '../types';
import Typography from './Typography';

/* eslint-disable react/no-unused-prop-types */
interface InputProps
  extends FormControlProps,
    InputHTMLAttributes<HTMLInputElement> {
  ref?: ForwardedRef<HTMLInputElement>;
}

function Input({
  label,
  helperText,
  error,
  id,
  ref,
  className,
  ...inputProps
}: InputProps) {
  return (
    <div
      className={clsx(className, 'nc-formControl', {
        'nc-formControl--hasError': error,
        'nc-formControl--isDisabled': inputProps.disabled,
      })}
    >
      {label && (
        <label
          className="nc-formControl__label"
          htmlFor={id || inputProps.name || 'unknown'}
        >
          {label}
        </label>
      )}
      <input
        data-cy={`nccom-form-${id
          ?.replaceAll(/[*]/g, '')
          .replaceAll(' ', '-')
          .toLowerCase()}-input`}
        ref={ref}
        className="nc-formControl__input"
        id={id || inputProps.name || 'unknown'}
        type="text"
        {...inputProps}
      />
      {helperText && (
        <Typography className="nc-formControl__helperText" variant="meta">
          {helperText}
        </Typography>
      )}
    </div>
  );
}

export default React.forwardRef<HTMLInputElement, InputProps>((props, ref) =>
  Input({ ...props, ref })
);
