import React, { useContext } from 'react';
import { CheckoutRoute, CustomerCartItem } from '../types';
import Button from './Button';
import Card from './Card';
import CartItem from './CartItem';
import CartSummaryPanel from './CartSummaryPanel';
import { checkoutContext } from './Checkout';
import DeliveryIcon from './icons/DeliveryIcon';
import Typography from './Typography';

export default function CheckoutOrderSummary() {
  const { customerCart, confirmStep, loading } = useContext(checkoutContext);

  const handleContinue = () => {
    confirmStep(CheckoutRoute.Summary);
  };

  return (
    <div className="nc-checkout">
      <div
        data-cy="nccom-checkout-order-summary"
        className="nc-checkout__main nc-checkout__main--summary"
      >
        <Card>
          {customerCart?.items?.map((cartItem) => (
            <CartItem
              key={cartItem?.uid}
              cartItem={cartItem as CustomerCartItem}
              loading={loading}
            />
          ))}
        </Card>
        <div
          data-cy="nccom-checkout-cart-list-footer"
          className="nc-cart__list__footer"
        >
          <Typography>Items are reserved for 72 hours</Typography>
          <Typography startAdornment={<DeliveryIcon />}>
            Free of charge – standard delivery
          </Typography>
        </div>
      </div>
      <Card className="nc-checkout__aside">
        {customerCart && (
          <CartSummaryPanel customerCart={customerCart}>
            <Button onClick={handleContinue} disabled={loading} fullWidth>
              Continue
            </Button>
          </CartSummaryPanel>
        )}
      </Card>
    </div>
  );
}
