import React, { useEffect, useState } from 'react';
import { useNotificationContext } from '../contexts/NotificationProvider';
import { useCustomerOrders } from '../graphql/queries';
import useFetchOptionsOverrides from '../hooks/useFetchOptionsOverrides';
import usePagination from '../hooks/usePagination';
import Debug from './dev/Debug';
import Divider from './Divider';
import Input from './Input';
import OrderItem from './OrderItem';
import Pagination from './Pagination';
import Skeleton from './Skeleton';
import Typography from './Typography';

export default function AccountOrderHistory() {
  const { pushNotification } = useNotificationContext();
  const fetchOptionsOverrides = useFetchOptionsOverrides();

  const {
    setNextPage,
    setPreviousPage,
    currentPage,
    setCurrentPage,
    pageSize,
    setPageSize,
    pageCount,
    setTotalCount,
    meta,
  } = usePagination();

  const [search, setSearch] = useState('');

  const { data, loading, error } = useCustomerOrders({
    fetchOptionsOverrides,
    variables: {
      currentPage: currentPage + 1,
      pageSize,
      search,
    },
  });

  useEffect(() => {
    if (error) {
      pushNotification({
        apiError: error,
      });
    }
  }, [error, pushNotification]);

  useEffect(() => {
    if (data?.customer?.orders?.total_count) {
      setTotalCount(data?.customer?.orders?.total_count);
    }
  }, [data?.customer?.orders?.total_count, setTotalCount]);

  const renderOrders = data?.customer?.orders?.total_count ? (
    <>
      <div
        data-cy="nccom-customer-orders-list"
        className="nc-customer__orders__list"
      >
        {data?.customer?.orders?.items.map(
          (order) => order && <OrderItem key={order.id} order={order} />
        )}
      </div>
      <Divider />
      <Pagination
        count={pageCount}
        currentPage={currentPage}
        onPageChange={(page) => setCurrentPage(page)}
        onPreviousPage={setPreviousPage}
        onNextPage={setNextPage}
        pageSize={pageSize}
        onPageSizeChange={setPageSize}
        meta={meta}
      />
    </>
  ) : (
    <Typography>You have no orders so far</Typography>
  );

  return (
    <>
      <header
        data-cy="nccom-account-orders-header"
        className="nc-customer__account__header"
      >
        <Typography variant="h3">Your Orders</Typography>
        <Input
          data-cy="nccom-customer-orders-searchbox"
          className="nc-customer__orders__search"
          placeholder="Search for order number"
          disabled={!data?.customer?.orders?.total_count}
          value={search}
          onChange={({ target: { value } }) => setSearch(value)}
        />
      </header>
      <Divider />
      {loading ? <Skeleton count={3} /> : renderOrders}

      <Debug data={data} />
    </>
  );
}
