import clsx from 'clsx';
import { Controller, RegisterOptions, useForm } from 'react-hook-form';
import {
  CountryCodeEnum,
  CustomerAddressInput,
  CustomerAddressRegionInput,
} from '../graphql/schema';
import { useSelector } from '../hooks';
import { getCountries, getRegionsMap } from '../store/ui';
import {
  CustomerAddressData,
  CustomerAddressFields,
  CustomerAddressFormData,
  CustomerAddressSelectFields,
  Option,
} from '../types';
import Button from './Button';
import Input from './Input';
import Select from './Select';

const fieldNameMap: {
  [key in CustomerAddressFields]: string;
} = {
  firstname: 'First name',
  lastname: 'Last name',
  street: 'Street',
  telephone: 'Phone',
  postcode: 'Zip',
  city: 'City',
  region: 'Region',
  country_code: 'Country',
};

const required = 'The field is required';
const nameValidation = {
  minLength: {
    value: 2,
    message: 'A name should be at least two characters long',
  },
};
const cityValidation = {
  minLength: {
    value: 2,
    message: 'A city should be at least two characters long',
  },
};

const fieldRegisterOptionMap: {
  [key in CustomerAddressFields]: RegisterOptions;
} = {
  firstname: {
    required,
    ...nameValidation,
  },
  lastname: {
    required,
    ...nameValidation,
  },
  street: {
    required,
  },
  telephone: {
    required,
    pattern: {
      value: /(\+\d\d)\d+/,
      message:
        'Use a international phone number starting with +digit/digit. A phone number can only contain numbers',
    },
  },
  postcode: {
    required,
    pattern: {
      value: /\d+/,
      message: 'A postcode can only contain numbers',
    },
  },
  city: {
    required,
    ...cityValidation,
  },
  country_code: {
    required,
  },
  region: {
    required,
  },
};

interface CustomerAddressFormProps {
  address: Partial<CustomerAddressData>;
  onChange?: (changeData: {
    isValid: boolean;
    formData: Partial<CustomerAddressInput>;
  }) => void;
  onSubmit?: (address: CustomerAddressInput) => void;
  onCancel?: () => void;
  hideCancel?: boolean;
  loading?: boolean;
  className?: string;
  submitLabel?: string;
  cancelLabel?: string;
  renderControls?: boolean;
  validateAllFieldsOnChange?: boolean;
}

/* eslint-disable jsx-a11y/role-supports-aria-props */
export default function CustomerAddressForm({
  address,
  onChange,
  onSubmit,
  onCancel,
  hideCancel,
  loading = false,
  className,
  submitLabel,
  cancelLabel,
  renderControls = true,
  validateAllFieldsOnChange = true,
}: CustomerAddressFormProps) {
  const defaultValues: CustomerAddressFormData = {
    firstname: address?.firstname || '',
    lastname: address?.lastname || '',
    street: address?.street?.join(', ') || '',
    telephone: address?.telephone || '',
    country_code: (address?.country_code || '') as CountryCodeEnum,
    region: address?.region?.region_id || '',
    postcode: address?.postcode || '',
    city: address?.city || '',
  };

  const {
    handleSubmit,
    formState,
    register,
    control,
    watch,
    getValues,
    trigger,
  } = useForm<CustomerAddressFormData>({
    mode: 'onChange',
    reValidateMode: 'onChange',
    defaultValues,
  });

  const countries = useSelector(getCountries);
  const regionsMap = useSelector(getRegionsMap);
  const currentCountryCode = watch('country_code');
  const countryRegions = regionsMap && regionsMap[currentCountryCode];

  const createRegionInput = (
    regionId: CustomerAddressFormData['region']
  ): CustomerAddressRegionInput | undefined => {
    const region = countryRegions?.find((item) => item?.id === regionId);
    return region
      ? {
          region: region.name,
          region_id: region.id,
          region_code: region.code,
        }
      : undefined;
  };

  const handleSubmitInternal = (formData: CustomerAddressFormData) => {
    if (onSubmit) {
      onSubmit({
        ...formData,
        region: createRegionInput(formData.region),
        street: formData.street ? formData.street.split(',') : undefined,
      });
    }
  };

  const handleChange = async () => {
    if (validateAllFieldsOnChange) {
      await trigger();
    }

    if (onChange) {
      const formData = getValues();

      onChange({
        isValid: formState.isValid,
        formData: {
          ...formData,
          region: createRegionInput(formData.region),
          street: formData.street ? formData.street.split(',') : undefined,
        },
      });
    }
  };

  const defaultOption: Option = {
    label: 'Please select',
    value: '',
  };

  const selectOptions: Record<CustomerAddressSelectFields, Option[]> = {
    country_code: [
      defaultOption,
      ...(countries?.map((country) => ({
        value: country?.id as string,
        label: country?.full_name_english as string,
      })) || []),
    ],
    region: countryRegions
      ? [
          defaultOption,
          ...countryRegions.map((region) => ({
            value: region?.id as number,
            label: region?.name as string,
          })),
        ]
      : [],
  };

  const renderFieldLabel = (
    name: keyof typeof fieldNameMap,
    isRequired?: boolean
  ) => `${fieldNameMap[name]}${isRequired && '*'}`;

  return (
    <form
      data-cy="nccom-form"
      className={clsx(className, 'nc-form')}
      onSubmit={handleSubmit(handleSubmitInternal)}
      onChange={handleChange}
    >
      {(
        Object.keys(defaultValues) as
          | CustomerAddressFields[]
          | CustomerAddressSelectFields[]
      ).map((name) =>
        Object.keys(selectOptions).includes(name) ? (
          <Controller
            control={control}
            key={name}
            name={name}
            rules={
              (selectOptions[name as CustomerAddressSelectFields] as Option[])
                ?.length
                ? fieldRegisterOptionMap[name]
                : undefined
            }
            render={({
              field: { onChange: selectOnChange, value, onBlur, ref },
            }) => (
              <Select
                ref={ref}
                label={renderFieldLabel(
                  name,
                  !!fieldRegisterOptionMap[name].required
                )}
                error={!!formState.errors[name]}
                helperText={formState.errors[name]?.message}
                onChange={(option) => {
                  selectOnChange({ target: { name, value: option.value } });
                }}
                onBlur={onBlur}
                value={value as string}
                options={
                  selectOptions[name as CustomerAddressSelectFields] || []
                }
                disabled={
                  !selectOptions[name as CustomerAddressSelectFields]?.length
                }
                withSearch
              />
            )}
          />
        ) : (
          <Input
            key={name}
            label={renderFieldLabel(name, true)}
            id={name}
            error={!!formState.errors[name as keyof typeof formState['errors']]}
            helperText={
              formState.errors[name as keyof typeof formState['errors']]
                ?.message
            }
            {...register(
              name as keyof typeof formState['errors'],
              fieldRegisterOptionMap[name]
            )}
          />
        )
      )}

      {renderControls && (
        <div className="nc-form__action">
          {!hideCancel && (
            <Button variant="secondary" onClick={onCancel} disabled={loading}>
              {cancelLabel || 'Cancel'}
            </Button>
          )}
          <Button type="submit" disabled={loading || !formState.isValid}>
            {submitLabel || 'Submit'}
          </Button>
        </div>
      )}
    </form>
  );
}
