import clsx from 'clsx';
import React from 'react';
import { ConfigurableProductOptions } from '../graphql/schema';
import { Option, ProductSelectedOptions } from '../types';
import Select from './Select';

interface ProductOptionSelectorProps {
  options: ConfigurableProductOptions[];
  selectedOptions: ProductSelectedOptions;
  onChange: (options: ProductSelectedOptions) => void;
  noDefault?: boolean;
  className?: string;
}

export default function ProductOptionSelector({
  options,
  selectedOptions,
  onChange,
  noDefault = false,
  className,
}: ProductOptionSelectorProps) {
  const handleChange = (option: Option, attributeUid: string) => {
    onChange({
      ...selectedOptions,
      [attributeUid]: option.value as string,
    });
  };

  const defaultOption = {
    label: 'Please select',
    uid: '',
  };

  const createOptions = (
    values: NonNullable<ConfigurableProductOptions['values']>
  ): Option[] =>
    (noDefault ? values : [defaultOption, ...values]).map((option) => ({
      label: option?.label as string,
      value: option?.uid as string,
    }));

  return (
    <div className={clsx(className)}>
      {options.map((attribute) => (
        <Select
          dataCyOverride={`nccom-${attribute.attribute_code}-toggle`}
          key={attribute.attribute_code}
          id={`id-${attribute.attribute_code}`}
          label={attribute.label as string}
          options={createOptions(attribute.values || [])}
          value={selectedOptions[attribute.uid as string]}
          onChange={(option) => handleChange(option, attribute.uid)}
        />
      ))}
    </div>
  );
}
