import { useEffect, useMemo, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useConfigurationContext } from '../contexts/ConfigurationProvider';
import { useNotificationContext } from '../contexts/NotificationProvider';
import { useProductDetail } from '../graphql/queries';
import { ConfigurableProductOptions } from '../graphql/schema';
import { useDispatch } from '../hooks';
import useProductOptionsSelection from '../hooks/useProductOptionsSelection';
import useVoucherSelection from '../hooks/useVoucherSelection';
import { setStageContent } from '../store/ui';
import { ProductDetailItem, ProductRouteParams } from '../types';
import Accordion from './Accordion';
import Gallery from './Gallery';
import HtmlDisplay from './HtmlDisplay';
import ProductAddToCartButton from './ProductAddToCartButton';
import ProductOptionSelector from './ProductOptionSelector';
import ProductPrice from './ProductPrice';
import ProductQuantitySelector from './ProductQuantitySelector';
import Skeleton from './Skeleton';
import Typography from './Typography';

export default function ProductDetail() {
  const { pushNotification } = useNotificationContext();
  const dispatch = useDispatch();

  const config = useConfigurationContext();
  const { productSku } = useParams<ProductRouteParams>();

  const { data, loading, error } = useProductDetail({
    variables: {
      filter: {
        sku: { eq: productSku },
      },
    },
  });

  useEffect(() => {
    if (error) {
      pushNotification({
        apiError: error,
      });
    }
  }, [error, pushNotification]);

  const product = useMemo<ProductDetailItem | null>(
    () =>
      data?.products?.items?.length
        ? (data?.products?.items[0] as ProductDetailItem)
        : null,
    [data]
  );
  const [quantity, setQuantity] = useState(1);
  const { selectedOptions, setSelectedOptions, selectionComplete } =
    useProductOptionsSelection(product?.configurable_options);

  const { productVouchers } = useVoucherSelection();

  // set stage
  useEffect(() => {
    dispatch(
      setStageContent(
        product
          ? {
              title: product.name as string,
              description: product.short_description?.html as string,
              loading,
            }
          : {
              title: 'Product Not Found',
              description:
                'We are sorry but the product you are looking for does not exist',
              loading,
            }
      )
    );
  }, [dispatch, loading, product]);

  return loading ? (
    <Skeleton variant="productDetail" />
  ) : (
    product && (
      <div
        data-cy="nccom-product-details-container"
        className="nc-productDetail"
      >
        <div className="nc-productDetail__display">
          {product.media_gallery && (
            <Gallery mediaGallery={product.media_gallery} />
          )}
        </div>
        <div className="nc-productDetail__order">
          <Typography variant="h3">
            <ProductPrice priceRange={product.price_range} />
          </Typography>
          {product.configurable_options && selectedOptions && (
            <ProductOptionSelector
              className="nc-productDetail__order__selector"
              options={
                product.configurable_options as ConfigurableProductOptions[]
              }
              selectedOptions={selectedOptions}
              onChange={(options) => setSelectedOptions(options)}
            />
          )}
          <ProductQuantitySelector
            className="nc-productDetail__order__quantity"
            value={quantity}
            onChange={setQuantity}
            maximumQuantity={
              productVouchers?.[product.sku as string]?.all?.length
            }
          />
          <ProductAddToCartButton
            className="nc-productDetail__order__addToCart"
            sku={product.sku as string}
            quantity={quantity}
            options={selectedOptions}
            disabled={!selectionComplete}
            voucherCode={
              productVouchers?.[product.sku as string]?.available?.[0] || null
            }
          />
        </div>
        <div
          data-cy="nccom-product-details"
          className="nc-productDetail__details"
        >
          {product.description?.html && (
            <Accordion label={config.productDetail.descriptionLabel} isOpen>
              <HtmlDisplay content={product.description?.html} />
            </Accordion>
          )}
        </div>
      </div>
    )
  );
}
