import { useEffect } from 'react';
import { Outlet, useLocation, useParams } from 'react-router-dom';
import { useCartContext } from '../contexts/CartContextProvider';
import {
  ConfigurationContext,
  useConfigurationContext,
} from '../contexts/ConfigurationProvider';
import { useDispatch } from '../hooks';
import { setStageContent } from '../store/ui';
import {
  EditorialRouteParams,
  MainRoute,
  ProductRouteParams,
  StageContent,
} from '../types';
import { isDevelopment } from './AppProvider';
import DevTool from './dev/DevTool';
import Stage from './Stage';

// TODO once clarified where the content will be fetched from establish hook or utility
function getStageContent(
  pathname: string,
  stageContents: ConfigurationContext['stageContents']
): StageContent {
  const index =
    Object.values(MainRoute).find((item) => pathname.startsWith(item)) ||
    'default';

  return stageContents[index];
}

export default function Page() {
  const dispatch = useDispatch();

  const { stageContents } = useConfigurationContext();
  const { productSku } = useParams<ProductRouteParams>();
  const { identifier } = useParams<EditorialRouteParams>();
  const { pathname } = useLocation();
  const {
    cart: { total_quantity },
  } = useCartContext();

  useEffect(() => {
    // TODO optimize/alter the strategy for setting the stage contents
    if (!productSku && !identifier && pathname !== MainRoute.Account) {
      const content = { ...getStageContent(pathname, stageContents) };

      if (pathname === MainRoute.Cart && !total_quantity) {
        content.description = undefined;
      }

      dispatch(setStageContent(content));
    }
  }, [
    dispatch,
    identifier,
    pathname,
    productSku,
    stageContents,
    total_quantity,
  ]);

  return (
    <>
      {!pathname.includes(MainRoute.Checkout) && <Stage />}
      <Outlet />

      {isDevelopment ? <DevTool /> : null}
    </>
  );
}
