import { useState } from 'react';
import { Link } from 'react-router-dom';
import { useSelector } from '../hooks';
import { AccountSection, MainRoute } from '../types';
import { ButtonProps } from './Button';
import Menu from './Menu';
import Typography from './Typography';

export default function WidgetProfile(props: ButtonProps) {
  const customer = useSelector((state) => state.user.customer);

  const initials = `${customer?.firstname?.charAt(
    0
  )}${customer?.lastname?.charAt(0)}`;

  const [open, setOpen] = useState(false);

  return (
    <div className="nc-widget__item">
      <button
        {...props}
        data-cy="nccom-widget-profile-avatar"
        className="nc-avatar"
        onClick={() => setOpen((state) => !state)}
      >
        {customer ? initials : '!'}
      </button>
      <Menu
        open={open}
        onClose={() => setOpen(false)}
        className="nc-widget__menu"
      >
        <Typography
          data-cy="nccom-profile-menu-header"
          className="nc-widget__header"
          variant="h4"
        >
          My Profile
        </Typography>
        <Typography
          data-cy="nccom-profile-menu-customer"
          className="nc-widget__customer"
        >
          Hello{' '}
          <strong>
            {customer?.firstname} {customer?.lastname}
          </strong>
        </Typography>
        <Link
          data-cy="nccom-account-link"
          className="nc-widget__link"
          to={MainRoute.Account}
          onClick={() => setOpen(false)}
        >
          Profile page
        </Link>
        <Link
          data-cy="nccom-order-history-link"
          className="nc-widget__link"
          to={`${MainRoute.Account}/${AccountSection.OrderHistory}`}
          onClick={() => setOpen(false)}
        >
          Order history
        </Link>
        <Link
          data-cy="nccom-address-book-link"
          className="nc-widget__link"
          to={`${MainRoute.Account}/${AccountSection.AddressBook}`}
          onClick={() => setOpen(false)}
        >
          Address book
        </Link>
      </Menu>
    </div>
  );
}
