import memCache from 'graphql-hooks-memcache';
import React, { useMemo } from 'react';
import { ClientContext, GraphQLClient } from 'graphql-hooks';
import { useConfigurationContext } from './ConfigurationProvider';

// eslint-disable-next-line react/jsx-no-constructed-context-values
// const client = new GraphQLClient({
//   url: 'http://3.123.231.160/graphql',
//   cache: memCache(),
// });

interface GraphQlClientProviderProps {
  children: React.ReactNode;
}

export default function GraphQlClientProvider({
  children,
}: GraphQlClientProviderProps) {
  const { magento } = useConfigurationContext();

  const client = useMemo(
    () =>
      new GraphQLClient({
        url: `${magento.url}/graphql`,
        cache: memCache(),
      }),
    [magento.url]
  );

  return (
    <ClientContext.Provider value={client}>{children}</ClientContext.Provider>
  );
}
