import clsx from 'clsx';
import React, { useCallback, useEffect, useRef, useState } from 'react';
import ReactDOM from 'react-dom';
import useClickAwayListener from '../hooks/useClickAwayListener';
import IconButton from './IconButton';
import CloseIcon from './icons/CloseIcon';

interface ModalProps {
  children: React.ReactNode;
  isOpen: boolean;
  onClose?: () => void;
  disableClose?: boolean;
  className?: string;
}

export default function Modal({
  children,
  isOpen: initialIsOpen,
  onClose,
  disableClose,
  className,
}: ModalProps) {
  let modalElement = document.getElementById('nccom-modal');

  // set up target for react portal
  if (!modalElement) {
    modalElement = document.createElement('div');
    modalElement.id = 'nccom-modal';
    document.body.appendChild(modalElement);
  }

  const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {
    setIsOpen(initialIsOpen);
  }, [initialIsOpen]);

  const handleClose = useCallback(() => {
    if (disableClose) return;

    setIsOpen(false);

    if (onClose) {
      onClose();
    }
  }, [disableClose, onClose]);

  // escape key listener
  useEffect(() => {
    const keyUpListener = ({ code }: KeyboardEvent) => {
      if (code === 'Escape') {
        handleClose();
      }
    };

    if (isOpen) {
      window.addEventListener('keyup', keyUpListener);
    } else {
      window.removeEventListener('keyup', keyUpListener);
    }

    return () => window.removeEventListener('keyup', keyUpListener);
  }, [handleClose, isOpen]);

  const modalContentRef = useRef(null);
  useClickAwayListener({
    containerRef: modalContentRef,
    onClickAway: () => {
      handleClose();
    },
  });

  return (
    modalElement &&
    ReactDOM.createPortal(
      isOpen ? (
        <div
          className={clsx(className, 'nc-modal', {
            'nc-modal--isOpen': isOpen,
          })}
        >
          <div
            data-cy="nccom-modal"
            ref={modalContentRef}
            className="nc-modal__content"
          >
            {children}
            <IconButton onClick={handleClose} disabled={disableClose}>
              <CloseIcon className="nc-modal__close-icon" size="small" />
            </IconButton>
          </div>
        </div>
      ) : null,
      modalElement
    )
  );
}
