import Keycloak, { KeycloakInitOptions, KeycloakOnLoad } from 'keycloak-js';

const getKeycloakUrl = () => {
  const root = document.getElementById('nccom-root');
  const keycloakAttribute = root?.getAttribute('data-keycloak-url');
  const keycloakUrl =
    keycloakAttribute || 'https://identity-test.netcentric.biz/auth';
  root?.removeAttribute('data-keycloak-url');
  return keycloakUrl;
};

// TODO consider using the configuration
// Setup Keycloak instance as needed
// Pass initialization options as required or leave blank to load from 'keycloak.json'
const keycloak = new Keycloak({
  url: getKeycloakUrl(),
  realm: 'netcentric',
  clientId: 'nc-shop-frontend',
});

export const keycloakProviderInitConfig: KeycloakInitOptions = {
  onLoad: 'check-sso' as KeycloakOnLoad,
  responseMode: 'query',
  checkLoginIframe: false,
};

export const eventLogger = (event: unknown, error: unknown) => {
  // eslint-disable-next-line no-console
  console.log('onKeycloakEvent', event, error);
};

export const tokenLogger = (tokens: unknown) => {
  // eslint-disable-next-line no-console
  console.log('onKeycloakTokens', tokens);
};

export default keycloak;
