import { useEffect, useMemo, useState } from 'react';
import { ConfigurableProductOptions } from '../graphql/schema';
import { ProductSelectedOptions } from '../types';

export default function useProductOptionsSelection(
  options?: (ConfigurableProductOptions | null)[] | null
) {
  const [selectedOptions, setSelectedOptions] =
    useState<ProductSelectedOptions | null>(null);

  useEffect(() => {
    if (!options) {
      setSelectedOptions(null);
      return;
    }

    setSelectedOptions(
      options.reduce(
        (acc, option) => ({
          ...acc,
          [(option as ConfigurableProductOptions).uid]: '',
        }),
        {}
      )
    );
  }, [options]);

  const selectionComplete = useMemo(
    () =>
      selectedOptions
        ? !Object.values(selectedOptions).some((optionValue) => !optionValue)
        : true,
    // TODO think of a way to make this `true` clearer as we don't set
    //  selectedOptions if there are no options means actually that the
    //  product is not configurable
    [selectedOptions]
  );

  return {
    selectedOptions,
    setSelectedOptions,
    selectionComplete,
  };
}
