import { PriceRange } from '../graphql/schema';
import { ProductItem } from '../types';

interface ProductPriceProps {
  priceRange: ProductItem['price_range'] | PriceRange;
}

export default function ProductPrice({ priceRange }: ProductPriceProps) {
  const {
    minimum_price: {
      regular_price: { currency, value },
    },
  } = priceRange;

  return (
    <strong data-cy="nccom-product-price">
      {value} {currency}
    </strong>
  );
}
