import clsx from 'clsx';
import { CustomerAddressData } from '../types';
import CustomerAddressDisplay, {
  CustomerAddressDisplayProps,
} from './CustomerAddressDisplay';
import RadioButton from './RadioButton';

interface CustomerAddressSelectProps
  extends Pick<CustomerAddressDisplayProps, 'displayDefaults'> {
  addresses: CustomerAddressData[];
  value?: CustomerAddressData['id'] | null;
  onChange?: (id: CustomerAddressData['id']) => void;
  className?: string;
  displaySingleSelection?: boolean;
}

export default function CustomerAddressSelect({
  addresses,
  value,
  onChange,
  className,
  displaySingleSelection = true,
  ...displayProps
}: CustomerAddressSelectProps) {
  return addresses.length > 1 || displaySingleSelection === true ? (
    <div
      data-cy="nccom-customer-address-select"
      className={clsx(className, 'nc-customer__addressSelect')}
    >
      {addresses.map((address) => (
        <RadioButton
          key={address.id}
          className="nc-customer__addressSelect__control"
          value={(address.id as number).toString()}
          checked={address.id === value}
          onChange={onChange ? () => onChange(address.id) : undefined}
          label={
            <CustomerAddressDisplay
              className="nc-customer__addressSelect__display"
              address={address}
              {...displayProps}
            />
          }
        />
      ))}
    </div>
  ) : (
    <CustomerAddressDisplay
      className="nc-customer__addressSelect__display"
      address={addresses[0]}
      {...displayProps}
    />
  );
}
