import clsx from 'clsx';
import React, { InputHTMLAttributes } from 'react';
import CheckmarkIcon from './icons/CheckmarkIcon';

export interface CheckboxProps extends InputHTMLAttributes<HTMLInputElement> {
  label: string | React.ReactNode;
  displayOnly?: boolean;
}

export default function Checkbox({
  label,
  id,
  className,
  displayOnly,
  ...checkboxProps
}: CheckboxProps) {
  return (
    // eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/no-noninteractive-element-interactions
    <label
      className={clsx(className, 'nc-formControlLabel', {
        'nc-formControlLabel--isDisabled': checkboxProps.disabled,
        'nc-formControlLabel--isDisplayOnly': displayOnly,
      })}
      htmlFor={id}
      onClick={(event) => (displayOnly ? event.preventDefault() : undefined)}
    >
      <input
        className="nc-formControlLabel__input"
        type="checkbox"
        id={id}
        {...checkboxProps}
      />
      <span className="nc-formControlLabel__control nc-formControlLabel__control--checkbox">
        <CheckmarkIcon
          className="nc-formControlLabel__control__icon"
          size="none"
        />
      </span>
      <span className="nc-formControlLabel__label">{label}</span>
    </label>
  );
}
