import { useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useNotificationContext } from '../contexts/NotificationProvider';
import { useCustomerVouchers } from '../graphql/queries';
import { Voucher } from '../graphql/schema';
import { useDispatch } from '../hooks';
import useFetchOptionsOverrides from '../hooks/useFetchOptionsOverrides';
import { setVouchers } from '../store/user';
import { AccountSection, MainRoute } from '../types';
import Card from './Card';
import CheckoutFinaleSun from './image/CheckoutFinaleSun';
import Typography from './Typography';

export default function CheckoutFinale() {
  const dispatch = useDispatch();
  const fetchOptionsOverrides = useFetchOptionsOverrides();
  const { data, error } = useCustomerVouchers({ fetchOptionsOverrides });
  const { pushNotification } = useNotificationContext();

  useEffect(() => {
    if (error) {
      pushNotification({
        apiError: error,
      });
    } else {
      dispatch(setVouchers((data?.customer?.vouchers as Voucher[]) || []));
    }
  }, [data, dispatch, error, pushNotification]);

  return (
    <Card variant="panel" className="nc-checkout__finale">
      <CheckoutFinaleSun
        data-cy="nccom-checkout-finale-sun"
        className="nc-checkout__finale__sun"
      />
      <div
        data-cy="nccom-checkout-finale-content"
        className="nc-checkout__finale__content"
      >
        <Typography variant="h4">Thank You for Your Order</Typography>
        <Typography autoMargin>
          Your order and payment will be confirmed by one of our admins. If you
          have any questions regarding your order write us on the slack-channel
          #nc-shop-customer
        </Typography>
        <Typography autoMargin>
          <Link to={MainRoute.Product}>Continue shopping</Link>
        </Typography>
        <Typography autoMargin>
          <Link to={`${MainRoute.Account}/${AccountSection.OrderHistory}`}>
            See purchase history
          </Link>
        </Typography>
      </div>
    </Card>
  );
}
