import clsx from 'clsx';
import React, { CSSProperties } from 'react';

interface CardProps {
  children: React.ReactNode;
  className?: string;
  variant?: 'elevation' | 'panel' | 'error' | 'success' | 'warning';
  style?: CSSProperties;
}

const Card = React.forwardRef<HTMLDivElement, CardProps>((props, ref) => {
  const { children, className, variant = 'elevation', style } = props;
  return (
    <div
      ref={ref}
      data-cy="nccom-card"
      className={clsx(className, 'nc-card', `nc-card--${variant}`)}
      style={style}
    >
      {children}
    </div>
  );
});

Card.displayName = 'Card';

export default Card;
