import { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { useNavigate, useParams } from 'react-router-dom';
import { useNotificationContext } from '../contexts/NotificationProvider';
import { useManualCmsPage } from '../graphql/queries';
import { setStageContent } from '../store/ui';
import { EditorialPage, EditorialRouteParams } from '../types';
import HtmlDisplay from './HtmlDisplay';
import Skeleton from './Skeleton';

export default function Editorial() {
  const { pushNotification } = useNotificationContext();
  const dispatch = useDispatch();
  const { identifier } = useParams<EditorialRouteParams>();
  const [content, setContent] = useState<EditorialPage | null>(null);
  const [contentHeading, setContentHeading] = useState<string | null>(null);
  const navigate = useNavigate();
  const [getCmsPage, { loading }] = useManualCmsPage();

  useEffect(() => {
    dispatch(
      setStageContent({
        title: contentHeading || '',
        loading,
      })
    );
  }, [dispatch, content, contentHeading, identifier, loading]);

  useEffect(() => {
    setContent(null);
    setContentHeading(null);
    getCmsPage({
      variables: {
        identifier: identifier || '',
      },
    }).then(({ data, error }) => {
      if (data?.cmsPage === null || error?.httpError?.status === 404) {
        navigate(`/${identifier}`);
      } else {
        if (data?.cmsPage?.content) {
          setContent(data?.cmsPage?.content);
        }
        if (data?.cmsPage?.content_heading) {
          setContentHeading(data?.cmsPage?.content_heading);
        }

        if (error) {
          pushNotification({
            apiError: error,
          });
        }
      }
    });
  }, [identifier, getCmsPage, pushNotification, navigate]);

  const renderContent = content ? (
    <div className="nc-editorial-page">
      <HtmlDisplay content={content} />
    </div>
  ) : null;

  return loading ? <Skeleton variant="default" count={4} /> : renderContent;
}
