import { useMemo, useState } from 'react';
import { useCartContext } from '../contexts/CartContextProvider';
import { Voucher } from '../graphql/schema';
import { ProductVoucher } from '../types';
import { useSelector } from './index';

export default function useVoucherSelection(multiple = true) {
  const { vouchers } = useSelector((state) => state.user);
  const { cart: { items: cartItems } = {} } = useCartContext();

  const groupedVouchers = vouchers.reduce<Record<string, string[]>>(
    (acc, voucher) => {
      const codes = [...(acc[voucher.sku] || []), voucher.code];

      return codes.length
        ? {
            ...acc,
            [voucher.sku]: codes,
          }
        : acc;
    },
    {}
  );

  const appliedVoucherCodes = (cartItems || [])
    ?.map((cartItem) =>
      groupedVouchers[cartItem?.product.sku as string]?.slice(
        0,
        cartItem?.quantity
      )
    )
    .flat();

  const couponCode = useMemo(
    () => appliedVoucherCodes.join(','),
    [appliedVoucherCodes]
  );

  const [selectedVouchers, setSelectedVouchers] = useState<Voucher[]>(
    multiple ? vouchers.map((item) => item) : [vouchers[0]]
  );

  const getVoucherIndex = (voucher: Voucher) =>
    selectedVouchers.findIndex(({ code }) => code === voucher.code);

  const hasVoucher = (voucher: Voucher) =>
    selectedVouchers.findIndex(({ code }) => code === voucher.code) > -1;

  const handleVoucher = (voucher: Voucher) => {
    const index = getVoucherIndex(voucher);

    if (!multiple) {
      setSelectedVouchers(index > -1 ? [] : [voucher]);
      return;
    }

    if (index === -1) {
      setSelectedVouchers((state) => [...state, voucher]);
    } else {
      const next = [...selectedVouchers];
      next.splice(index, 1);
      setSelectedVouchers(next);
    }
  };

  const productVouchers: ProductVoucher = vouchers.reduce<ProductVoucher>(
    (acc, item) => {
      const all = [...(acc[item.sku]?.all || []), item.code];
      const available = all.filter(
        (code) => !appliedVoucherCodes.includes(code)
      );
      const applied = all.filter((code) => appliedVoucherCodes.includes(code));

      return all.length
        ? {
            ...acc,
            [item.sku]: {
              productName: item.product_name,
              all,
              available,
              applied,
            },
          }
        : acc;
    },
    {}
  );

  return {
    vouchers: vouchers.filter((item) =>
      appliedVoucherCodes.includes(item?.code)
    ),
    availableVouchers: vouchers.filter(
      (item) => !appliedVoucherCodes.includes(item?.code)
    ),
    couponCode,
    appliedVoucherCodes,
    productVouchers,
    selectedVouchers,
    handleVoucher,
    hasVoucher,
  };
}
