import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import { useNotificationContext } from '../contexts/NotificationProvider';
import { usePlaceOrder } from '../graphql/queries';
import { BillingCartAddress, ShippingCartAddress } from '../graphql/schema';
import { useSelector } from '../hooks';
import useFetchOptionsOverrides from '../hooks/useFetchOptionsOverrides';
import { CheckoutRoute, CustomerCartItem, MainRoute } from '../types';
import Button from './Button';
import Card from './Card';
import CartItem from './CartItem';
import CartSummaryPanel from './CartSummaryPanel';
import { checkoutContext } from './Checkout';
import CheckoutSection from './CheckoutSection';
import CustomerAddressDisplay from './CustomerAddressDisplay';
import EditIcon from './icons/EditIcon';
import Typography from './Typography';

export default function CheckoutConfirmation() {
  const { pushNotification } = useNotificationContext();

  const { customerCart, confirmStep } = useContext(checkoutContext);

  const { cartId } = useSelector(({ user }) => user);
  const [placeOrder, { loading }] = usePlaceOrder();
  const fetchOptionsOverrides = useFetchOptionsOverrides();

  const handlePlaceOrder = async () => {
    const { error } = await placeOrder({
      fetchOptionsOverrides,
      variables: {
        cartId: cartId as string,
      },
    });

    if (!error) {
      confirmStep(CheckoutRoute.Confirmation);
    } else {
      pushNotification({ apiError: error });
    }
  };

  return (
    <div className="nc-checkout">
      <div
        data-cy="nccom-checkout-confirmation"
        className="nc-checkout__main nc-checkout__main--confirmation"
      >
        {customerCart?.items?.map((cartItem) => (
          <Card className="nc-checkout__confirmationCard" key={cartItem?.uid}>
            <CartItem cartItem={cartItem as CustomerCartItem} hideActions />
            <Link
              data-cy="nccom-checkout-confirmation-card-edit"
              className="nc-checkout__confirmationCard__edit"
              to={`${MainRoute.Checkout}/${CheckoutRoute.Summary}`}
            >
              <EditIcon />
            </Link>
          </Card>
        ))}

        {/* Products, EMail, Delivery Address, Billing Address, Payment Method */}
        {/* Recheck Figma */}

        <Card className="nc-checkout__confirmationCard nc-checkout__confirmationCard--display">
          <CheckoutSection header="Shipping Method">
            <Typography>
              {
                customerCart?.shipping_addresses[0]?.selected_shipping_method
                  ?.method_title
              }
            </Typography>
          </CheckoutSection>
        </Card>
        <Card className="nc-checkout__confirmationCard nc-checkout__confirmationCard--display">
          <CheckoutSection
            header="Delivery Address"
            action={
              <Link
                data-cy="checkout-edit-shipping-link"
                to={`${MainRoute.Checkout}/${CheckoutRoute.Shipping}`}
              >
                <EditIcon />
              </Link>
            }
            dense
          >
            <CustomerAddressDisplay
              address={
                customerCart?.shipping_addresses[0] as ShippingCartAddress
              }
            />
          </CheckoutSection>
        </Card>
        <Card className="nc-checkout__confirmationCard nc-checkout__confirmationCard--display">
          <CheckoutSection header="Payment Method">
            <Typography>
              Pay with product{' '}
              {customerCart?.items && customerCart?.items.length > 1
                ? 'vouchers'
                : 'voucher'}
            </Typography>
          </CheckoutSection>
        </Card>
        <Card className="nc-checkout__confirmationCard nc-checkout__confirmationCard--display">
          <CheckoutSection
            header="Billing Address"
            action={
              <Link
                data-cy="checkout-edit-billing-link"
                to={`${MainRoute.Checkout}/${CheckoutRoute.Payment}`}
              >
                <EditIcon />
              </Link>
            }
            dense
          >
            <CustomerAddressDisplay
              address={customerCart?.billing_address as BillingCartAddress}
            />
          </CheckoutSection>
        </Card>
      </div>
      <Card className="nc-checkout__aside">
        {customerCart && (
          <CartSummaryPanel customerCart={customerCart}>
            <Button fullWidth onClick={handlePlaceOrder} disabled={loading}>
              Confirm order
            </Button>
          </CartSummaryPanel>
        )}
      </Card>
    </div>
  );
}
