import { useConfigurationContext } from '../contexts/ConfigurationProvider';
import SlackLogo from './image/SlackLogo';

interface SlackLinkProps {
  className?: string;
}

export default function SlackLink({ className }: SlackLinkProps) {
  const {
    contact: { support },
  } = useConfigurationContext();

  return (
    <a
      href={support}
      target="blank"
      title="Get support on our Slack channel"
      className={className}
    >
      <SlackLogo />
    </a>
  );
}
