import { MutableRefObject, useEffect } from 'react';

interface ClickAwayListenerProps {
  onClickAway: () => void;
  containerRef: MutableRefObject<null | HTMLElement>;
  rootElement?: Document | HTMLElement | Element;
  disable?: boolean;
}

export default function useClickAwayListener({
  onClickAway,
  containerRef,
  rootElement = document,
  disable,
}: ClickAwayListenerProps) {
  useEffect(() => {
    if (disable || !containerRef.current) return undefined;

    const clickHandler = (event: Event) => {
      if (
        containerRef.current &&
        !containerRef.current.contains(event.target as Node)
      ) {
        onClickAway();
        rootElement.removeEventListener('click', clickHandler);
      }
    };

    rootElement.addEventListener('click', clickHandler);

    return () => {
      rootElement.removeEventListener('click', clickHandler);
    };
  }, [containerRef, disable, onClickAway, rootElement]);
}
