import clsx from 'clsx';
import React from 'react';
import { NavLink } from 'react-router-dom';
import { CheckoutStep } from '../types';
import CheckmarkIcon from './icons/CheckmarkIcon';
import Typography from './Typography';

function Step({
  step,
  allComplete,
  children,
}: {
  step: CheckoutStep;
  allComplete: boolean;
  children: React.ReactNode;
}) {
  const stepProps = {
    title: step.label,
    className: clsx('nc-checkoutStepper__step', {
      'nc-checkoutStepper__step--isConfirmed': step.confirmed,
    }),
  };

  return !allComplete && step.path && (step.confirmed || step.current) ? (
    <NavLink {...stepProps} to={step.path}>
      {children}
    </NavLink>
  ) : (
    <div {...stepProps}>{children}</div>
  );
}

interface CheckoutStepperProps {
  steps: CheckoutStep[];
  allComplete: boolean;
  className?: string;
}

export default function CheckoutStepper({
  steps,
  allComplete,
  className,
}: CheckoutStepperProps) {
  return (
    <div
      data-cy="nccom-checkout-stepper"
      className={clsx(className, 'nc-checkoutStepper')}
    >
      {steps.map((step) => (
        <Step key={step.index} step={step} allComplete={allComplete}>
          {/* TODO use simple checkmark icon once available */}
          <div
            data-cy={`nccom-checkout-stepper-step-${step.index}-icon`}
            className="nc-checkoutStepper__step__icon"
          >
            {step.confirmed ? <CheckmarkIcon size="none" /> : step.index}
          </div>
          <Typography
            className="nc-checkoutStepper__step__label"
            variant="meta"
            element="div"
          >
            {step.label}
          </Typography>
        </Step>
      ))}
    </div>
  );
}
