import React, { useContext } from 'react';
import { useNotificationContext } from '../contexts/NotificationProvider';
import { useSetPaymentMethod } from '../graphql/queries';
import { useSelector } from '../hooks';
import useCreateCheckoutAddress from '../hooks/useCreateCheckoutAddress';
import useFetchOptionsOverrides from '../hooks/useFetchOptionsOverrides';
import useUpdateCouponCode from '../hooks/useUpdateCouponCode';
import { CheckoutRoute, CustomerAddressData } from '../types';
import Button from './Button';
import Card from './Card';
import CartSummaryPanel from './CartSummaryPanel';
import Checkbox from './Checkbox';
import { checkoutContext } from './Checkout';
import CheckoutSection from './CheckoutSection';
import CustomerAddressForm from './CustomerAddressForm';
import CustomerAddressSelect from './CustomerAddressSelect';
import Skeleton from './Skeleton';
import Typography from './Typography';

export default function CheckoutPayment() {
  const { pushNotification } = useNotificationContext();

  const {
    customerCart,
    confirmStep,
    contextData: { shippingAddressId, billingAddressId, billingIsShipping },
    setBillingAddressId,
    setBillingIsShipping,
  } = useContext(checkoutContext);

  const { cartId, customer } = useSelector(({ user }) => user);
  const fetchOptionsOverrides = useFetchOptionsOverrides();

  const filteredAddresses = billingIsShipping
    ? ([
        customer?.addresses?.find((item) => item?.id === shippingAddressId),
      ] as CustomerAddressData[])
    : (customer?.addresses?.filter(
        (item) => item?.id !== shippingAddressId
      ) as CustomerAddressData[]);

  // address creation
  const { createAddress, setCreateAddress, handleCreateCustomerAddress, busy } =
    useCreateCheckoutAddress(setBillingAddressId);

  const [setPaymentMethod, { loading }] = useSetPaymentMethod();

  const { updateCouponCode } = useUpdateCouponCode();

  // TODO separate ?: handles payment method and billing address
  const handleSetPaymentMethod = async () => {
    if (!billingAddressId) return;

    // We need to apply coupon before setting up the payment
    // method because it will alter the available methods.
    await updateCouponCode();

    const { error } = await setPaymentMethod({
      fetchOptionsOverrides,
      variables: {
        cartId: cartId as string,
        paymentMethodCode: 'free',
        billingId: billingAddressId,
      },
    });

    if (!error) {
      confirmStep(CheckoutRoute.Payment);
    } else {
      pushNotification({ apiError: error });
    }
  };

  return (
    <div className="nc-checkout">
      {loading ? (
        <Skeleton variant="checkout" />
      ) : (
        <>
          <Card className="nc-checkout__main">
            <CheckoutSection header="Payment Details">
              <Typography>Payment with product voucher from wallet.</Typography>
            </CheckoutSection>
            <CheckoutSection header="Billing Address" dense={!createAddress}>
              <Checkbox
                data-cy="nccom-billing-address-checkbox"
                label="Delivery address is the billing address"
                id="setSameAsShipping"
                checked={billingIsShipping || false}
                onChange={() => {
                  setBillingIsShipping();
                  if (customer?.addresses?.length) {
                    setCreateAddress(
                      !createAddress && customer?.addresses?.length === 1
                    );
                  }
                }}
              />
              {!billingIsShipping &&
              (!filteredAddresses.length || createAddress) ? (
                <CustomerAddressForm
                  className="nc-checkout__shipping__addressForm"
                  address={{
                    firstname: customer?.firstname as string,
                    lastname: customer?.lastname as string,
                  }}
                  loading={busy}
                  onSubmit={handleCreateCustomerAddress}
                  onCancel={() => {
                    setCreateAddress(false);
                    if (!filteredAddresses.length) setBillingIsShipping();
                  }}
                  hideCancel={!customer?.addresses?.length}
                />
              ) : (
                <CustomerAddressSelect
                  displayDefaults="billing"
                  displaySingleSelection={!billingIsShipping}
                  addresses={filteredAddresses || []}
                  value={billingAddressId as number}
                  onChange={(id) => setBillingAddressId(id)}
                />
              )}
              {!billingIsShipping &&
              filteredAddresses.length &&
              !createAddress ? (
                <Button
                  onClick={() => {
                    setCreateAddress(true);
                  }}
                  variant="secondary"
                >
                  Add a new address
                </Button>
              ) : null}
            </CheckoutSection>
            <CheckoutSection>
              <Typography data-cy="nccom-terms-conditions-text" variant="meta">
                Disclaimer: If we are unable to deliver the goods after two
                attempts, we will try to find a safe secure place to leave your
                parcel. We will leave a note explaining where your parcel is and
                how You can rearrange delivery. If You are not going to be at
                the delivery location at the time agreed, please contact us to
                rearrange delivery for another mutually convenient day.
              </Typography>
            </CheckoutSection>
          </Card>
          <Card className="nc-checkout__aside">
            {customerCart && (
              <CartSummaryPanel customerCart={customerCart}>
                <Button
                  onClick={handleSetPaymentMethod}
                  fullWidth
                  disabled={loading || busy || !billingAddressId}
                >
                  Confirm payment
                </Button>
              </CartSummaryPanel>
            )}
          </Card>
        </>
      )}
    </div>
  );
}
