import Select from './Select';

interface ProductQuantitySelectorProps {
  value: number;
  onChange: (quantity: number) => void;
  className?: string;
  loading?: boolean;
  maximumQuantity?: number;
}

export default function ProductQuantitySelector({
  value: inputValue,
  onChange,
  className,
  loading,
  maximumQuantity = 0,
}: ProductQuantitySelectorProps) {
  return (
    <Select
      className={className}
      label="Quantity"
      value={inputValue}
      disabled={loading || maximumQuantity === 1}
      options={Array(maximumQuantity === 0 ? 1 : maximumQuantity)
        .fill(null)
        .map((_, index) => ({
          label: `${index + 1}`,
          value: index + 1,
        }))}
      onChange={onChange ? ({ value }) => onChange(Number(value)) : undefined}
    />
  );
}
