import { useEffect, useMemo, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { useConfigurationContext } from '../contexts/ConfigurationProvider';
import { useProductList } from '../graphql/queries';
import {
  ConfigurableProduct,
  ConfigurableProductOptions,
} from '../graphql/schema';
import useProductOptionsSelection from '../hooks/useProductOptionsSelection';
import useVoucherSelection from '../hooks/useVoucherSelection';
import { AccountSection, MainRoute } from '../types';
import { ButtonProps } from './Button';
import Divider from './Divider';
import IconButton from './IconButton';
import WalletIcon from './icons/WalletIcon';
import Menu from './Menu';
import ProductAddToCartButton from './ProductAddToCartButton';
import ProductOptionSelector from './ProductOptionSelector';
import RadioButton from './RadioButton';
import Typography from './Typography';
import WidgetCounter from './WidgetCounter';

export default function WidgetWallet(props: ButtonProps) {
  const config = useConfigurationContext();
  const { pathname } = useLocation();
  const { availableVouchers, productVouchers } = useVoucherSelection(false);

  const { data } = useProductList({
    variables: {
      filter: {},
    },
  });

  const [selectedProductSku, setSelectedProductSku] = useState(
    Object.keys(productVouchers)[0] || null
  );

  const selectedProduct = useMemo(
    () =>
      selectedProductSku &&
      data?.products?.items?.find(
        (product) => product?.sku === selectedProductSku
      ),
    [data?.products?.items, selectedProductSku]
  );

  const isConfigurableProduct = !!(selectedProduct as ConfigurableProduct)
    ?.configurable_options;

  const { selectedOptions, setSelectedOptions, selectionComplete } =
    useProductOptionsSelection(
      (selectedProduct as ConfigurableProduct)?.configurable_options || null
    );

  const [open, setOpen] = useState(false);

  useEffect(() => {
    setOpen(false);
  }, [pathname]);

  return (
    <div className="nc-wallet nc-widget__item">
      <IconButton
        data-cy="nccom-widget-wallet-icon-button"
        {...props}
        onClick={() => setOpen((state) => !state)}
      >
        <WalletIcon className="nc-wallet__icon" size="none" />
        <WidgetCounter
          dataCy="nccom-widget-wallet-count"
          count={availableVouchers.length}
        />
      </IconButton>
      <Menu
        className="nc-widget__menu"
        open={open}
        onClose={() => setOpen(false)}
      >
        <Typography
          data-cy="nccom-wallet-menu-header"
          className="nc-widget__header"
          variant="h4"
        >
          My Wallet
        </Typography>
        {availableVouchers.length ? (
          <>
            <Typography>Vouchers available</Typography>
            <div data-cy="nccom-wallet-menu-list" className="nc-wallet__list">
              {Object.entries(productVouchers).map(([sku, voucher]) =>
                voucher.available?.length ? (
                  <RadioButton
                    key={sku}
                    className="nc-wallet__list__control"
                    checked={selectedProductSku === sku}
                    onChange={() => setSelectedProductSku(sku)}
                    label={
                      <span
                        data-cy="nccom-wallet-menu-list-item"
                        className="nc-wallet__list__item"
                      >
                        <span
                          data-cy="nccom-wallet-menu-list-quantity"
                          className="nc-wallet__list__quantity"
                        >
                          <WalletIcon />
                          <span
                            data-cy="nccom-wallet-menu-list-quantity-count-icon"
                            className="nc-wallet__list__quantity__count"
                          >
                            {voucher.available?.length}
                          </span>
                        </span>
                        {voucher.productName}
                      </span>
                    }
                  />
                ) : null
              )}
            </div>
            {isConfigurableProduct && selectedOptions && (
              <ProductOptionSelector
                selectedOptions={selectedOptions}
                onChange={setSelectedOptions}
                options={
                  (selectedProduct as ConfigurableProduct)
                    .configurable_options as ConfigurableProductOptions[]
                }
              />
            )}
          </>
        ) : (
          <Typography>
            No vouchers available. Visit{' '}
            <a target="blank" href={config.bonusly}>
              Bonus.ly
            </a>
          </Typography>
        )}
        {selectedProductSku && (
          <ProductAddToCartButton
            className="nc-wallet__checkout"
            sku={selectedProductSku}
            label="Select product & add to cart"
            options={isConfigurableProduct ? selectedOptions : undefined}
            voucherCode={productVouchers[selectedProductSku]?.available?.[0]}
            disabled={
              !selectedProduct || (isConfigurableProduct && !selectionComplete)
            }
            visible={availableVouchers.length > 0}
          />
        )}
        <Divider />
        <Typography>
          <Link
            data-cy="nccom-wallet-menu-link"
            to={`${MainRoute.Account}/${AccountSection.OrderHistory}`}
          >
            See purchase history
          </Link>
        </Typography>
      </Menu>
    </div>
  );
}
