import { KeycloakProvider } from '@react-keycloak/web';
import React from 'react';
import { Provider as StoreProvider } from 'react-redux';
import { HashRouter } from 'react-router-dom';
import ConfigurationProvider from '../contexts/ConfigurationProvider';
import GraphQlClientProvider from '../contexts/GraphQlClientProvider';
import NotificationProvider from '../contexts/NotificationProvider';
import keycloak, {
  eventLogger,
  keycloakProviderInitConfig,
  tokenLogger,
} from '../keycloak';
import store from '../store';

export const isDevelopment = process.env.NODE_ENV === 'development';

interface AppProvidersProps {
  children: React.ReactNode;
}

export default function AppProvider({ children }: AppProvidersProps) {
  return (
    <ConfigurationProvider>
      <KeycloakProvider
        keycloak={keycloak}
        initConfig={keycloakProviderInitConfig}
        onEvent={isDevelopment ? eventLogger : undefined}
        onTokens={isDevelopment ? tokenLogger : undefined}
      >
        <GraphQlClientProvider>
          <StoreProvider store={store}>
            <HashRouter>
              <NotificationProvider>{children}</NotificationProvider>
            </HashRouter>
          </StoreProvider>
        </GraphQlClientProvider>
      </KeycloakProvider>
    </ConfigurationProvider>
  );
}
