import { useConfigurationContext } from '../contexts/ConfigurationProvider';
import { SocialPlatformType } from '../types';
import FacebookIcon from './icons/FacebookIcon';
import InstagramIcon from './icons/InstagramIcon';
import TwitterIcon from './icons/TwitterIcon';

export default function FooterSocials() {
  const { socials } = useConfigurationContext();

  const renderIcon = (type: SocialPlatformType) => {
    if (type === 'instagram') return <InstagramIcon size="small" />;
    if (type === 'facebook') return <FacebookIcon size="small" />;
    if (type === 'twitter') return <TwitterIcon size="small" />;
    return null;
  };

  return (
    <div className="nc-footer__socials">
      {socials.map((social) => (
        <a key={social.type} target="blank" href={social.link}>
          {renderIcon(social.type)}
        </a>
      ))}
    </div>
  );
}
