import React, { useContext, useEffect, useMemo, useState } from 'react';
import config, { NcComConfiguration } from '../config';

export type ConfigurationContext = NcComConfiguration;
const context = React.createContext<ConfigurationContext>({
  ...config,
});

export function useConfigurationContext() {
  return useContext(context);
}

interface ConfigurationProviderProps {
  children: React.ReactNode;
}

export default function ConfigurationProvider({
  children,
}: ConfigurationProviderProps) {
  const [url, setUrl] = useState(config.magento.url);

  useEffect(() => {
    const root = document.getElementById('nccom-root');
    const urlAttribute = root?.getAttribute('data-config-url');
    if (urlAttribute) {
      setUrl(urlAttribute);
      root?.removeAttribute('data-config-url');
    }
  }, []);

  const contextValue = useMemo(
    () => ({
      ...config,
      magento: {
        ...config.magento,
        url,
      },
    }),
    [url]
  );

  return <context.Provider value={contextValue}>{children}</context.Provider>;
}
