import { Navigate, RouteObject } from 'react-router-dom';
import Account from './components/Account';
import { isDevelopment } from './components/AppProvider';
import Cart from './components/Cart';
import Checkout from './components/Checkout';
import CheckoutConfirmation from './components/CheckoutConfirmation';
import CheckoutFinale from './components/CheckoutFinale';
import CheckoutOrderSummary from './components/CheckoutOrderSummary';
import CheckoutPayment from './components/CheckoutPayment';
import CheckoutShipping from './components/CheckoutShipping';
import DevLayout from './components/dev/DevLayout';
import Styleguide from './components/dev/Styleguide';
import Editorial from './components/Editorial';
import NotFound from './components/NotFound';
import Page from './components/Page';
import ProductDetail from './components/ProductDetail';
import ProductList from './components/ProductList';
import AddressManagementProvider from './contexts/AddressManagementProvider';
import { CheckoutRoute, MainRoute } from './types';

const devRoutes: RouteObject = {
  path: '/dev',
  element: <DevLayout />,
  children: [
    {
      index: true,
      element: <Styleguide />,
    },
    {
      path: 'styleguide',
      element: <Styleguide />,
    },
  ],
};

export const checkoutRoutes: RouteObject[] = [
  {
    index: true,
    element: <Navigate to={CheckoutRoute.Summary} replace />,
  },
  {
    path: CheckoutRoute.Summary,
    element: <CheckoutOrderSummary />,
  },
  {
    path: CheckoutRoute.Shipping,
    element: (
      <AddressManagementProvider>
        <CheckoutShipping />
      </AddressManagementProvider>
    ),
  },
  {
    path: CheckoutRoute.Payment,
    element: (
      <AddressManagementProvider>
        <CheckoutPayment />
      </AddressManagementProvider>
    ),
  },
  {
    path: CheckoutRoute.Confirmation,
    element: <CheckoutConfirmation />,
  },
  {
    path: CheckoutRoute.Finale,
    element: <CheckoutFinale />,
  },
];

const defaultRoutes: RouteObject[] = [
  {
    path: '/',
    element: <Page />,
    children: [
      {
        index: true,
        element: <Navigate to={MainRoute.Product} replace />,
      },
      {
        path: MainRoute.Product,
        element: <ProductList />,
      },
      {
        path: `${MainRoute.Product}/:productSku`,
        element: <ProductDetail />,
      },
      {
        path: MainRoute.Checkout,
        element: <Checkout />,
        children: checkoutRoutes,
      },
      {
        path: MainRoute.Account,
        element: <Account />,
        children: [
          { index: true, element: <Account /> },
          { path: ':section', element: <Account /> },
        ],
      },
      {
        path: `${MainRoute.Editorial}/:identifier`,
        element: <Editorial />,
      },
      {
        path: MainRoute.Cart,
        element: <Cart />,
      },
    ],
  },
  {
    path: '*',
    element: <NotFound />,
  },
];

const getRoutes = () => {
  if (isDevelopment) defaultRoutes.push(devRoutes);
  return defaultRoutes;
};

const routes: RouteObject[] = getRoutes();

export const checkoutPaths = checkoutRoutes
  .filter((route) => !route.index)
  .map((route) => route.path as CheckoutRoute);

export default routes;
